import React, { useEffect, useState } from 'react'

import { ButtonGroup, Grid, Typography } from '@material-ui/core'
import { Add } from '@material-ui/icons'
import { makeStyles } from '@material-ui/styles'

import moment from 'moment'

import { DateRangePicker, Range } from 'react-date-range'

import { TSelectWithNumberFormat } from 'modules/TSelectWithNumberFormat'
import { IFilterAdvanceProps } from 'modules/IFilterAdvance/index.d'

import { TButton } from 'components/TButton'

import {
  TITLE_CREATE_TASK,
  LABEL_DAY,
  LABEL_LIST,
  LABEL_MONTH,
  LABEL_WEEK,
  DATE_FORMAT_MAIN,
} from 'constants/common'
import { OPTIONS_GROUP_BY } from 'constants/defaultValue'

import { parseGroupByOptions } from 'utils/renderComponent'

import 'react-date-range/dist/styles.css' // main style file
import 'react-date-range/dist/theme/default.css' // theme css file

const useStyles = makeStyles(() => ({
  buttonGroup: {
    marginBottom: '16px',
    marginRight: '10px',

    '& button': {
      margin: '0 !important'
    }
  },
  agendaButton: {
    margin: '0 !important'
  },
  groupByButton: {
    minWidth: '160px'
  }
}))


export const IFilterAdvance = (props: IFilterAdvanceProps): JSX.Element => {
  const { values, onChange, onChangeAgenda, onToggleModal } = props
  const classes = useStyles()

  const [toggleDatePicker, setToggleDatePicker] = useState<boolean>(false)
  const [dateRange, setDateRange] = useState<Range[]>()

  useEffect(() => {
    setDateRange([
      {
        startDate: moment(values.fromDate).toDate(),
        endDate: moment(values.toDate).toDate(),
        key: 'selection',
      }
    ])
  }, [values.fromDate, values.toDate])
  const renderLabelPicker = `${moment(values.fromDate).format(DATE_FORMAT_MAIN)} - ${moment(values.toDate).format(
    DATE_FORMAT_MAIN,
  )}`

  let clsFlexContainer: 'flex-start' | 'flex-end' = 'flex-end'
  let toggleClsAgendaNavigation = 'hide'
  if (values.view === 'agenda') {
    toggleClsAgendaNavigation = 'show'
    clsFlexContainer = 'flex-start'
  }

  const renderButtonType = (viewMode: string): JSX.Element => {
    let updateView = 'list'
    if (viewMode === 'list') {
      updateView = 'agenda'
    }
    if (viewMode === 'agenda') {
      updateView = 'list'
    }

    return <TButton className={classes.agendaButton} disabled={values.view === 'agenda'} label={LABEL_LIST} variant="contained" onClick={() => onChange(updateView)} />
  }


  return (
    <Grid className="filter-advance" container spacing={2} justifyContent={clsFlexContainer}>
      <Grid className={`filter-agenda ${toggleClsAgendaNavigation}`} item xs={5}>
        <Grid container>
          <TButton
            className="label-picker-btn"
            label={renderLabelPicker}
            variant="outlined"
            onClick={() => setToggleDatePicker(!toggleDatePicker)}
          />
          {toggleDatePicker && (
            <Typography className="date-range-picker" component="div">
              <DateRangePicker
                direction="horizontal"
                months={2}
                moveRangeOnFirstSelection={false}
                ranges={dateRange}
                onChange={(item: any) => {
                  setDateRange([item.selection])
                  if (item.selection.startDate !== item.selection.endDate) {
                    onChangeAgenda(item.selection)
                    setToggleDatePicker(false)
                  }
                }}
              />
            </Typography>
          )}
          <TSelectWithNumberFormat
            id="filterGroupBy"
            name="groupBy"
            className={classes.groupByButton}
            options={parseGroupByOptions(OPTIONS_GROUP_BY)}
            value={values.groupBy}
            onChange={onChange}
          />
        </Grid>
      </Grid>
      <Grid className="filter-type" item xs={7}>
        <Grid>
          <ButtonGroup className={classes.buttonGroup} aria-label="outlined primary button group">
            <TButton
              disabled={values.view === 'day'}
              label={LABEL_DAY}
              variant="contained"
              onClick={() => onChange('day', 'list')}
            />
            <TButton
              disabled={values.view === 'week'}
              label={LABEL_WEEK}
              variant="contained"
              onClick={() => onChange('week', 'list')}
            />
            <TButton
              disabled={values.view === 'month'}
              label={LABEL_MONTH}
              variant="contained"
              onClick={() => onChange('month', 'list')}
            />
          </ButtonGroup>

          {renderButtonType(values.mode)}

          <TButton
            className="btn-add"
            color="primary"
            label={TITLE_CREATE_TASK}
            startIcon={<Add />}
            variant="outlined"
            onClick={() => onToggleModal(true)}
          />
        </Grid>
      </Grid>
    </Grid>
  )
}
