import React, { useCallback, useContext, useEffect, useState, useMemo } from 'react'

import isArray from 'lodash/isArray'

import { CommonContext } from 'context/CommonContext'

import { ISelectTeamProps } from 'modules/ISelectTeam/index.d'
import { TAutocomplete } from 'components/TAutocomplete'

import { getTeamList } from 'services/api/GetOptions'

import { LABEL_TEAM, RESPONSE_GET_SUCCESS, PLACEHOLDER_TEAM, PLACEHOLDER_TEAM_HAS_VALUE } from 'constants/common'

import { parseTeamOptions } from 'utils/renderComponent'
import { IOption } from 'utils/form'
import { isNotEmpty } from 'utils/util'

export const ISelectTeam = (props: ISelectTeamProps): JSX.Element => {
  const { setLoading } = useContext(CommonContext)

  const { errMsg, multiple, placeholder = PLACEHOLDER_TEAM, required, value, zoneIds, onChange } = props
  const convertToNumber = (isArray(value) && value) || ''

  const itemName = 'teamIds'

  const memoPlaceholder = useMemo(() => {
    return isNotEmpty(value) ? PLACEHOLDER_TEAM_HAS_VALUE : placeholder
  }, [placeholder, value])

  const [optionItems, setOptionItems] = useState<IOption[]>([])

  const fetchData = useCallback(async (): Promise<void> => {
    let parseData: IOption[] = []
    if (zoneIds && zoneIds.length > 0) {
      setLoading(true)
      const items = await getTeamList(zoneIds)
      setLoading(false)

      if (items.code === RESPONSE_GET_SUCCESS) {
        // check data exist and length
        if (items.data || (items.data && items.data.length > 0)) {
          // parse response items
          parseData = parseTeamOptions(itemName, items.data)
        }
      }
    }

    // update options
    setOptionItems(parseData)
  }, [zoneIds])

  useEffect(() => {
    fetchData()
  }, [zoneIds])

  const handleChange = (name: string, val: null | number | number[]): void => {
    if (multiple) {
      onChange(name, val as number[])
    } else {
      onChange(name, val ? [val as number] : [])
    }
  }

  return (
    <TAutocomplete
      error={errMsg}
      id={itemName}
      label={LABEL_TEAM}
      multiple={multiple}
      name={itemName}
      option={optionItems}
      placeholder={memoPlaceholder}
      required={required}
      value={convertToNumber}
      onChange={handleChange}
    />
  )
}
