import React from 'react'
import { Typography } from '@material-ui/core'

import { TLabelValueProps } from 'components/TLabelValue/index.d'

import 'components/TLabelValue/index.scss'

export const TLabelValue = (props: TLabelValueProps): JSX.Element => {
  const { className, label, value, variant } = props

  return (
    <Typography className={`style-label-container ${className}`} component="div">
      {label && (
        <Typography className="style-label" variant={variant}>
          {label}
        </Typography>
      )}

      <Typography className="style-value" component="div" variant={variant}>
        {value}
      </Typography>
    </Typography>
  )
}
