import React from 'react'
import { Typography } from '@material-ui/core'

import { TRenderFileProps } from 'components/TRenderFile/index.d'

import 'components/TRenderFile/index.scss'

export const TRenderFile = (props: TRenderFileProps): JSX.Element => {
  const { className, component, filename, mimetype, src } = props

  let attachedFileType
  switch (mimetype.toLowerCase()) {
    case 'ppt': {
      attachedFileType = `${process.env.REACT_APP_PUBLIC_ICON_PATH}/icon-ppt.png`
      break
    }
    case 'excel': {
      attachedFileType = `${process.env.REACT_APP_PUBLIC_ICON_PATH}/icon-excel.png`
      break
    }
    case 'word': {
      attachedFileType = `${process.env.REACT_APP_PUBLIC_ICON_PATH}/icon-word.png`
      break
    }
    case 'pdf': {
      attachedFileType = `${process.env.REACT_APP_PUBLIC_ICON_PATH}/icon-pdf.svg`
      break
    }
    default:
      attachedFileType = src
      break
  }

  return (
    <Typography className={`file-type ${className}`} component={component}>
      <img src={attachedFileType} alt={filename} />
    </Typography>
  )
}
