import React, { useState } from 'react'

import { ThemeProvider } from '@material-ui/styles'
import { Theme } from 'Theme'

import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'

import { CommonContext } from 'context/CommonContext'

import { PageError401 } from 'pages/Errors/401'
import { TodoDetail } from 'pages/TodoDetail'
import { TodoList } from 'pages/TodoList'

import { ILoading } from 'components/ILoading'

import { getPemissionOnAction } from 'services/api/Task'
import { ITodoResponse } from 'services/api/api.d'

import 'App.scss'

let currentUserId = 448
if (process.env.NODE_ENV === 'production') {
  const getWindow: any = (window as any).window
  currentUserId = getWindow.currentUser.userId
}

export const App = (): JSX.Element => {
  const [loading, setLoading] = useState(false)
  const [respStatus, setRespStatus] = useState(false)

  const checkPermission = async (act: string): Promise<ITodoResponse<any>> => {
    // set Loading
    setLoading(true)
    const resp = await getPemissionOnAction(act)
    setLoading(false)

    return resp
  }

  const dataItemContext = {
    currentUserId,
    loading,
    respStatus,
    checkPermission,
    setLoading,
    setRespStatus,
  }

  return (
    <ThemeProvider theme={Theme}>
      <div className="App">
        <CommonContext.Provider value={dataItemContext}>
          <Router>
            <Switch>
              <Route exact path="/calendar-task">
                <TodoList />
              </Route>
              <Route exact path="/calendar-task/detail/:id">
                <TodoDetail />
              </Route>
              <Route path="*" render={PageError401} />
            </Switch>
          </Router>
          <ILoading status={loading} />
        </CommonContext.Provider>

        {/* <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.tsx</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>

        <HelloWorld color="green" name="Tuan 12344" />
        <CommentView
          onDataChange={onDataChange}
          categoryConfigData={CategoryConfig}
          commentSuggestionResponse={CommentSuggestionResponse}
          errorsConfig={[]}
          onCommentPriceTagChange={onCommentPriceTagChange}
        />
      </header> */}
      </div>
    </ThemeProvider>
  )
}
