import React from 'react'
import { Typography } from '@material-ui/core'

import { TFile } from 'pages/TodoDetail/index.d'

import 'modules/RenderAttachedFile/index.scss'
import { TRenderFile } from 'components/TRenderFile'
import { IMAGE_TYPE } from 'constants/common'

export const RenderAttachedFile = (props: TFile): JSX.Element => {
  const { className, name, type, url, size, onClick } = props

  const renderContentFile = (src?: string): JSX.Element => {
    if (IMAGE_TYPE.indexOf(type.toLowerCase()) > -1) {
      return <TRenderFile component="div" filename={name} mimetype={type} src={src} />
    }

    return (
      <a href={src} title="Download" download>
        <TRenderFile component="div" filename={name} mimetype={type} src={src} />
        <Typography className="file-info" component="div">
          <Typography className="filename">{name}</Typography>
          <Typography className="filesize">{size}</Typography>
        </Typography>
      </a>
    )
  }

  const clickOnImage = (): string | number => {
    if (onClick) {
      return onClick()
    }

    return ''
  }

  return (
    <Typography className={`file-container ${className}`} component="div" onClick={() => clickOnImage()}>
      {renderContentFile(url)}
    </Typography>
  )
}
