import { useEffect, useRef, useCallback } from 'react'
import { v4 as uuidv4 } from 'uuid'
import taskTrackingService from 'services/taskTrackingService'
import useLatestValue from './useLatestValue'

function useViewDetailTaskTracking(task: any): void {
  const eventSource = useRef<string>(localStorage.getItem('view_detail_task_tracking') || 'unknown')
  const eventTransactionId = useRef(uuidv4())
  const currentTask = useLatestValue(task)

  const trackingEvent = (event: string): void => {
    if (eventSource.current && eventTransactionId.current) {
      const {id, completionAllDay, completionTimeDate, completionTimeFrom, completionTimeTo, reminderTime, status} = currentTask.current || {}

      taskTrackingService.sendEvent({
        eventName: `cal_user_${event}_view_detail_task`,
        timestamp: +new Date(),
        eventTransactionId: eventTransactionId.current,
        eventData: {
          eventSourceCode: eventSource.current,
          id,
          completionAllDay,
          completionTimeDate,
          completionTimeFrom,
          completionTimeTo,
          reminderTime,
          status
        }
      })
    }
  }

  const handleVisibilitychange = useCallback(() => {
    if (document.visibilityState === 'hidden') {
      trackingEvent('end')
    } else {
      trackingEvent('start')
    }
  }, [])

  useEffect(() => {
    localStorage.removeItem('view_detail_task_tracking')

    taskTrackingService.connect()

    trackingEvent('start')

    document.addEventListener('visibilitychange', handleVisibilitychange)

    return () => {
      trackingEvent('end')

      taskTrackingService.disconnect()

      document.removeEventListener('visibilitychange', handleVisibilitychange)
    }
  }, [])
}

export default useViewDetailTaskTracking
