import React from 'react'
import { FormHelperText } from '@material-ui/core'

import { TSelectWithNumberFormatProps } from 'modules/TSelectWithNumberFormat/index.d'
import { ISelectOption } from 'components/ISelectOption'

export const TSelectWithNumberFormat = (props: TSelectWithNumberFormatProps): JSX.Element => {
  const { className, error, disabled, id, label, name, options, placeholder, required, value, onChange } = props

  let getPlaceholder = placeholder
  if (!placeholder) {
    getPlaceholder = options[0].label
  }

  return (
    <>
      <ISelectOption
        autoWidth={false}
        className={className}
        disabled={disabled}
        id={id}
        label={label}
        name={name}
        option={options}
        placeholder={getPlaceholder}
        required={required}
        value={value}
        onChange={onChange}
      />
      <FormHelperText id="component-error-text">{error}</FormHelperText>
    </>
  )
}
