import React, { useEffect, useRef } from 'react'
import confetti from 'canvas-confetti'
import { Dialog, Typography, IconButton } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import CloseIcon from '@material-ui/icons/Close'

import { randomInRange } from 'utils/util'

import { ICompleteTaskConfettiPopup } from 'components/CompleteTaskConfettiPopup/index.d'

import 'components/CompleteTaskConfettiPopup/styles.scss'

const useStyles = makeStyles((theme) => ({
  confettiTitle: {
    fontWeight: 400,
    marginBottom: theme.spacing(1),
  },
}))

export const CompleteTaskConfettiPopup = (props: ICompleteTaskConfettiPopup): JSX.Element => {
  const { show, handleClose } = props

  const confettiRef = useRef<HTMLCanvasElement>(null)
  const timeoutRef = useRef<NodeJS.Timeout>()
  const confettiIntervalId = useRef<NodeJS.Timeout>()

  const createConfetti = (): void => {
    const myConfetti = confetti.create(confettiRef.current as HTMLCanvasElement, { resize: true })

    const angles = [135, 90, 45]
    const originX = [1, 0.5, 0]

    const getConfettiRandom = (index: number): any => ({
      particleCount: 500,
      angle: angles[index],
      spread: 90,
      startVelocity: 45,
      decay: 0.9,
      ticks: 60,
      origin: {
        y: 1.2,
        x: originX[index],
      },
    })

    myConfetti(getConfettiRandom(1))

    confettiIntervalId.current = setInterval(() => {
      if (document.visibilityState === 'visible') {
        const random = randomInRange(0, 2)
        myConfetti(getConfettiRandom(random))
      }
    }, 1500)
  }

  const awaitDomReady = (): void => {
    timeoutRef.current = setTimeout(() => {
      if (confettiRef.current) {
        createConfetti()
      } else {
        awaitDomReady()
      }
    }, 500)
  }

  useEffect(() => {
    if (show) {
      awaitDomReady()
    }

    return () => {
      if (confettiIntervalId.current) {
        clearInterval(confettiIntervalId.current)
      }

      if (timeoutRef.current) {
        clearInterval(timeoutRef.current)
      }
    }
  }, [show])

  const classes = useStyles()

  return (
    <Dialog fullWidth maxWidth="xs" open={show} onClose={() => handleClose()}>
      <div className="complete-task-confetti">
        <IconButton className="complete-task-confetti__close" onClick={() => handleClose()}>
          <CloseIcon fontSize="large" />
        </IconButton>

        <Typography className={classes.confettiTitle} variant="h2" color="primary">
          Chc mng!
        </Typography>

        <Typography variant="h4">Bn va hon tt cng vic</Typography>

        <canvas className="confetti" ref={confettiRef} />
      </div>
    </Dialog>
  )
}
