export const renderTrangThai = (status?: number, title?: string): JSX.Element => {
  let clsName = ''
  let label = title
  switch (status) {
    case 2:
      clsName = 'completed'
      if (!title) {
        label = LABEL_STATUS_COMPLETED
      }
      break
    case 1:
      clsName = 'in-completed'
      if (!title) {
        label = LABEL_STATUS_IN_COMPLETED
      }
      break
    case 3:
      clsName = 'cancelled'
      if (!title) {
        label = LABEL_STATUS_CANCELLED
      }
      break
    default:
      break
  }

  return <Chip className={`status-highlight ${clsName}`} label={label} />
}

export const renderPriority = (status?: number | string, title?: string): JSX.Element => {
  let icon = ''
  let label = title
  switch (status) {
    case 1:
      icon = 'lowest.svg'
      if (!title) {
        label = PRIORITY_LABEL_LOW
      }
      break
    case 2:
      icon = 'medium.svg'
      if (!title) {
        label = PRIORITY_LABEL_MEDIUM
      }
      break
    case 3:
      icon = 'highest.svg'
      if (!title) {
        label = PRIORITY_LABEL_HIGHEST
      }
      break

    default:
      icon = 'lowest.svg'
      if (!title) {
        label = PRIORITY_LABEL_LOW
      }
      break
  }

  return <TLabelIcon className="item-value" icon={`${process.env.REACT_APP_PUBLIC_ICON_PATH}/${icon}`} label={label} />
}

export const renderLienQuanDen = (props: ILienQuanDenProps): JSX.Element => {
  const { name, id, label } = props
  let link = ''
  if (label === 'Deal') {
    link = `/deal/detail/${id}`
  }
  if (label === 'Listing') {
    link = `/pos/sa/detail/${id}`
  }

  return (
    <Typography className="deal" component="div">
      {label}{' '}
      <a className="highlight-id" href={link} rel="noreferrer" target="_blank">
        {id}
      </a>{' '}
      - <Typography component="span">{name}</Typography>
    </Typography>
  )
}

export const renderFile = (fileItems: TFile[], cls: string, toggleLightBox?: (id: number) => number): JSX.Element[] => {
  let onClick: () => number | string

  return fileItems.map((item, k) => {
    if (toggleLightBox) {
      onClick = () => toggleLightBox(k)
    }

    return (
      <RenderAttachedFile
        className={cls}
        key={`${item.url}`}
        name={item.name}
        size={item.size}
        type={item.type}
        url={item.url}
        onClick={onClick}
      />
    )
  })
}

export const renderFileList = (items: TFile[]): JSX.Element => {
  return (
    <TLabelValue key="file" className="mt-20" label={`Files (${items.length})`} value={renderFile(items, 'two-cols')} />
  )
}
export const renderImageList = (items: TFile[], toggleLightBox?: (i: number) => number): JSX.Element => {
  return (
    <TLabelValue
      key="image"
      className="mt-20"
      label={`Hnh nh (${items.length})`}
      value={renderFile(items, 'two-cols hide-info mt-10', toggleLightBox)}
    />
  )
}
export const renderAttachedFiles = (items: TAttachedFile, toggleLightBox: (i: number) => number): JSX.Element[] => {
  const listFiles = []
  if (items.mFile.length > 0) {
    listFiles.push(renderFileList(items.mFile))
  }
  if (items.image.length > 0) {
    listFiles.push(renderImageList(items.image, toggleLightBox))
  }

  return listFiles.map((listFile) => listFile)
}

export const renderDateTime = (dt: string): JSX.Element => {
  /**
   * TODO need to define dt object to compare with now time()
   */
  let clsName = 'item-value error'
  if (dt) {
    clsName = 'item-value'
  }

  return <TTitle className={clsName} title={dt} />
}

export const renderLightbox = (
  open: boolean,
  images: TFile[],
  i: number,
  setOpen: (_: boolean) => void,
  setPhotoIndex: (_: number) => void,
): JSX.Element => {
  if (open) {
    return (
      <Lightbox
        mainSrc={images[i].url}
        nextSrc={images[(i + 1) % images.length].url}
        prevSrc={images[(i + images.length - 1) % images.length].url}
        onCloseRequest={() => setOpen(!open)}
        onMovePrevRequest={() => setPhotoIndex((i + images.length - 1) % images.length)}
        onMoveNextRequest={() => setPhotoIndex((i + 1) % images.length)}
      />
    )
  }

  return <p />
}

