export const parseOptions = (itemName: string, options: TOptionInput[], icons?: JSX.Element[]): IOption[] => {
  let parseItems: IOption[] = []
  let itemIcon: JSX.Element

  options.map((item, k) => {
    itemIcon = <></>
    if (icons && icons[k]) {
      itemIcon = icons[k]
    }

    parseItems = [
      ...parseItems,
      {
        icon: itemIcon,
        key: `${itemName}-${item.id}`,
        label: item.name,
        value: item.id,
      },
    ]

    return parseItems
  })

  return parseItems
}

