export const parseCalendarItem = (data: IResponseTask[], groupBy: number, fromDate?: Date, toDate?: Date): Event[] => {
  let parseItems: Event[] = []

  data.map((item) => {
    let end = moment(item.completionTimeTo).toDate()
    let start = moment(item.completionTimeFrom).toDate()
    if (item.completionAllDay) {
      end = moment(getEndDate(moment(item.completionTimeDate).toDate(), 'day')).toDate()
      start = moment(getStartDate(moment(item.completionTimeDate).toDate(), 'day')).toDate()
    }
    parseItems = [
      ...parseItems,
      {
        allDay: item.completionAllDay,
        end,
        resource: { ...item, groupBy, fromDate, toDate },
        start,
        title: item.name,
      },
    ]

    return item
  })

  return parseItems
}

