export const renderStatusColor = (status: number): string => {
  let clsEventStatus = ''
  if (status === TaskStatusType.COMPLETED) {
    clsEventStatus = 'completed'
  }
  if (status === TaskStatusType.INCOMPLETE) {
    clsEventStatus = 'in-completed'
  }
  if (status === TaskStatusType.CANCELED) {
    clsEventStatus = 'cancel'
  }

  return clsEventStatus
}
export const MyCustomHeader = ({ date }: HeaderProps): JSX.Element => {
  const toCapitalStr = upperFirst(moment(date).format('dddd'))
  return <Typography className="custom-header" component="div">{toCapitalStr}</Typography>
}

export const MyEvent = (props: any): JSX.Element => {
  const { event } = props
  const { resource } = event

  const renderTooltipLabel = `${renderEventTime(resource.completionTimeFrom, resource.completionTimeTo, resource.completionAllDay)} ${resource.name}`

  return (
    <Tooltip title={renderTooltipLabel} aria-label={renderTooltipLabel}>
      <Typography className="event-item" component="div">
        <Typography className={`status-highlight ${renderStatusColor(resource.status)}`} component="span">
          &nbsp;
        </Typography>
        <Typography className="priority" component="div">
          {renderPriority(resource.priority)}
        </Typography>
        <Typography className="time" component="span">
          {renderEventTime(resource.completionTimeFrom, resource.completionTimeTo, resource.completionAllDay)}
        </Typography>
        <Typography className="title" component="span">
          {resource.name}
        </Typography>
      </Typography>
    </Tooltip>
  )
}
export const MyMoreEvent = (props: any): JSX.Element => {
  const { events, currentMode } = props

  const handleClickTaskName = ({ id }: { id: number }): void => {
    localStorage.setItem(
      'view_detail_task_tracking',
      JSON.stringify({
        source: 'calendar-view-more',
        mode: currentMode || '',
      }),
    )

    window.open(`/calendar-task/detail/${id}`, '_blank')
  }

  const useStyles = makeStyles(() => ({
    taskName: {
      cursor: 'pointer',
      marginRight: '6px',
    },
  }))

  const classes = useStyles()

  return (
    <Typography className="event-item-list" component="div">
      {events.map((event: Event) => {
        const { resource } = event

        return (
          <Typography key={`event-item-${resource.id}`} className="event-item" component="div">
            <Typography className="priority" component="div">
              {renderPriority(resource.priority)}
            </Typography>
            <Typography className="time" component="div">
              {renderEventTime(resource.completionTimeFrom, resource.completionTimeTo, resource.completionAllDay, true)}
            </Typography>
            <Typography
              onClick={() => handleClickTaskName({ id: resource.id })}
              className={classes.taskName}
              component="a"
            >
              {resource.name}
            </Typography>
            <Typography className="status-highlight-container" component="div">
              {renderTrangThai(resource.status)}
            </Typography>
          </Typography>
        )
      })}
    </Typography>
  )
}

export const CustomToolbar = (props: TToolbar): JSX.Element | null => {
  const { mode } = props

  const useStyles = makeStyles(() => ({
    navigateButton: {
      padding: '5px',
      minWidth: 'initial'
    },
    navigateButtonRight: {
      marginRight: '16px'
    },
    todayButton: {
      padding: '5px 15px'
    },
  }))

  const classes = useStyles()


  // not render Toolbar Navigation if mode is agenda
  if (mode === 'agenda') {
    return null
  }

  const { toolbar, prevView, onHandleNavigate } = props
  const { date, label, view, onNavigate } = toolbar

  const getLabel = convertDateText(label)

  const goToBack = (): void => {
    const dateToNavigate = date.setDate(date.getDate())

    if (view === 'day') {
      date.setDate(date.getDate() - 1)
    }
    if (view === 'week') {
      date.setDate(date.getDate() - 7)
    }
    if (view === 'month') {
      date.setMonth(date.getMonth() - 1)
      // dateToNavigate = date.setMonth(date.getMonth())
    }

    const { fromDate, toDate } = renderDateFromTo({ date, prevView, view })
    onNavigate('PREV', dateToNavigate)
    onHandleNavigate({ fromDate, toDate })
  }

  const goToNext = (): void => {
    const dateToNavigate = date.setDate(date.getDate())
    if (view === 'day') {
      date.setDate(date.getDate() + 1)
    }
    if (view === 'week') {
      date.setDate(date.getDate() + 7)
    }
    if (view === 'month') {
      date.setMonth(date.getMonth() + 1)
      // dateToNavigate = date.setMonth(date.getMonth())
    }

    const { fromDate, toDate } = renderDateFromTo({ date, prevView, view })
    onNavigate('NEXT', dateToNavigate)
    onHandleNavigate({ fromDate, toDate })
  }

  const goToCurrent = (): void => {
    date.setDate(date.getDate())
    const { fromDate, toDate } = renderDateFromTo({ date, prevView: 'day', view: 'agenda' })

    onNavigate('TODAY', date)
    onHandleNavigate({ fromDate, toDate })
  }

  return (
    <Grid container>
      <Typography className="toolbar-container" component="div">
        <TButton className={classes.navigateButton} startIcon={<NavigateBeforeIcon />} variant="outlined" onClick={goToBack} />
        <TButton className={classes.todayButton} label={LABEL_TODAY} variant="outlined" onClick={goToCurrent} />
        <TButton className={`${classes.navigateButton} ${classes.navigateButtonRight}`} startIcon={<NavigateNextIcon />} variant="outlined" onClick={goToNext} />

        <Typography component="label" className="label-date">
          {getLabel}
        </Typography>
      </Typography>
    </Grid>
  )
}
