import React, { useCallback, useEffect, useState } from 'react'

import { IViewAssignedToProps } from 'modules/IViewAssignedTo/index.d'

import { TLabelValue } from 'components/TLabelValue'
import { TTitle } from 'components/TTitle'

import { RESPONSE_GET_SUCCESS } from 'constants/common'
import { AssignerResponse } from 'constants/mockupData'
import { IOption } from 'utils/form'
import { parseAssigneddOptions } from 'utils/renderComponent'
import { getAssigneeToList } from 'services/api/GetOptions'

export const IViewAssignedTo = (props: IViewAssignedToProps): JSX.Element => {
  const { label, value, assignedToName } = props
  const [title, setTitle] = useState(assignedToName || '')

  const itemName = 'assignedTo'

  const defItems = parseAssigneddOptions(itemName, AssignerResponse)
  const [optionItems, setOptionItems] = useState<IOption[]>(defItems)

  const fetchData = useCallback(async (): Promise<void> => {
    const items = await getAssigneeToList()

    if (items.code === RESPONSE_GET_SUCCESS) {
      // check data exist and length
      if (items.data || (items.data && items.data.length > 0)) {
        // parse response items
        const parseData = parseAssigneddOptions(itemName, items.data)

        // update options
        setOptionItems(parseData)
      }
    }
  }, [optionItems])

  useEffect(() => {
    fetchData()
  }, [])

  useEffect(() => {
    if (optionItems?.length) {
      const matchedUser = optionItems.find(item => item.value === value)

      setTitle(matchedUser?.label || assignedToName || '')
    }
  }, [optionItems])

  return (
    <TLabelValue
      className="no-padding"
      label={label}
      value={<TTitle className="item-value" title={<span>{title}</span>} />}
    />
  )
}
