import { useState } from 'react'

import { VALIDATION_FIELD } from 'utils/util'

type TListFields = {
  [x: string]: string | number
}
interface TUseForControls {
  handleInputValue: any
  handleFormSubmit: any
  handleListValue: any
  handleReset: any
  handleResetValidationFields: any
  // eslint-disable-next-line no-unused-vars
  validate: (fieldValues: any) => void
  formIsValid: any
  errors: any
  values: any
}

export const useFormControls = (initialFormValues: any, validateFields: VALIDATION_FIELD): TUseForControls => {
  // We'll update "values" as the form updates
  // eslint-disable-next-line no-unused-vars
  const [values, setValues] = useState(initialFormValues)

  // set state for validated fields
  const [validatedFields, setValidatedFields] = useState(validateFields)

  // "errors" is used to check the form for errors
  const [errors, setErrors] = useState({} as any)

  const validate = (fieldValues = values): void => {
    // this function will check if the form values are valid
    const temp: any = { ...errors }

    const getKey = Object.keys(fieldValues)[0]
    const getValue = Object.values(fieldValues)[0]

    if (validatedFields[getKey]) {
      temp[getKey] = null
      if (validatedFields[getKey].rule.required) {
        if (!getValue) {
          temp[getKey] = `Vui lng nhp ${validatedFields[getKey].label}`
        }
      }

      setErrors((prev: any) => ({
        ...prev,
        [getKey]: temp[getKey]
      }))
    }
  }

  const handleInputValue = (fieldName: string, fieldValue: any, updatedValidateFields: VALIDATION_FIELD): void => {
    // this function will be triggered by the text field's onBlur and onChange events
    setValues((prev: any) => ({
      ...prev,
      [fieldName]: fieldValue,
    }))

    validate({ [fieldName]: fieldValue })
    if (updatedValidateFields) {
      validate(updatedValidateFields)
    }
  }

  const handleListValue = (listFields: TListFields): void => {
    setValues({
      ...values,
      ...listFields,
    })
  }

  const handleSuccess = (): void => {
    setValues({
      ...initialFormValues,
      formSubmitted: true,
      success: true,
    })
  }

  const handleError = (): void => {
    setValues({
      ...initialFormValues,
      formSubmitted: true,
      success: false,
    })
  }

  const handleReset = (data: any): void => {
    setValues(data)
    setErrors({})
  }
  const handleResetValidationFields = (fields: VALIDATION_FIELD): void => {
    setValidatedFields(fields)
  }

  const formIsValid = (data = values): any => {
    // this function will check if the form values and return a boolean value
    let isValid = true

    const arrKeys = Object.keys(data)
    arrKeys.forEach((key) => {
      if (validatedFields[key]) {
        if (validatedFields[key].rule.required) {
          if (!data[key]) {
            isValid = false
          }
        }
      }
    })

    return isValid
  }

  async function handleFormSubmit(form: any): Promise<any> {
    // this function will be triggered by the submit event
    const isValid = Object.values(errors).every((x) => x === '') && formIsValid()
    if (isValid) {
      return form(values, handleSuccess, handleError)
    }

    return errors
  }

  return {
    errors,
    formIsValid,
    values,
    handleInputValue,
    handleFormSubmit,
    handleListValue,
    handleReset,
    handleResetValidationFields,
    validate
  }
}
