import React from 'react'

import { IViewLocationProps } from 'modules/IViewLocation/index.d'

import { LABEL_LOCATION } from 'constants/common'
import { TLabelValue } from 'components/TLabelValue'
import { TTitle } from 'components/TTitle'

export const IViewLocation = (props: IViewLocationProps): JSX.Element => {
  const { value } = props

  return (
    <TLabelValue
      className="no-padding"
      label={LABEL_LOCATION}
      value={<TTitle className="item-value" title={<span>{value}</span>} />}
    />
  )
}
