import React, { useCallback, useEffect, useState } from 'react'

import { IViewReminderProps } from 'modules/IViewReminder/index.d'

import { TLabelValue } from 'components/TLabelValue'
import { TTitle } from 'components/TTitle'

import { getReminderList } from 'services/api/GetOptions'

import { parseOptions } from 'utils/renderComponent'
import { IOption } from 'utils/form'

import { LABEL_REMINDER, RESPONSE_GET_SUCCESS } from 'constants/common'
import { JobTypeResponse } from 'constants/mockupData'

export const IViewReminder = (props: IViewReminderProps): JSX.Element => {
  const { value } = props
  const [title, setTitle] = useState('')

  const itemName = 'reminderTime'

  const defItems = parseOptions(itemName, JobTypeResponse)
  const [optionItems, setOptionItems] = useState<IOption[]>(defItems)

  const fetchData = useCallback(async (): Promise<void> => {
    const items = await getReminderList()

    if (items.code === RESPONSE_GET_SUCCESS) {
      // check data exist and length
      if (items.data || (items.data && items.data.length > 0)) {
        // parse response items
        let parseData = parseOptions(itemName, items.data)
        parseData = optionItems.concat(parseData)

        // update options
        setOptionItems(parseData)
      }
    }
  }, [])

  useEffect(() => {
    fetchData()
  }, [])

  useEffect(() => {
    const filterItem = optionItems.filter((item) => item.value === value)
    if (filterItem.length > 0) {
      setTitle(filterItem[0].label)
    }
  }, [optionItems])

  return (
    <TLabelValue
      className="no-padding"
      label={LABEL_REMINDER}
      value={<TTitle className="item-value" title={title} />}
    />
  )
}
