import React, { useState } from 'react'

import { Calendar, momentLocalizer, Event } from 'react-big-calendar'

import { Dialog, Typography } from '@material-ui/core'

import moment from 'moment'
import 'moment/locale/vi'

import { upperFirst } from 'lodash'

import { ICalendarProps, CalendarLanguagesMode } from 'modules/ICalendar/index.d'

import { currentTime } from 'constants/defaultValue'
import { DAY_HEADER_FORMAT, LABEL_ALL_DAY, MONTH_HEADER_FORMAT, LANGUAGES, TASK } from 'constants/common'

import { CustomToolbar, MyCustomHeader, MyEvent, MyMoreEvent } from 'utils/renderComponent'
import { TNavigate } from 'utils/renderComponent.d'
import useCalendarModeTracking from 'utils/useCalendarModeTracking'

import 'react-big-calendar/lib/css/react-big-calendar.css'
import 'modules/ICalendar/index.scss'
import { convertDateText, getStartDate } from 'utils/util'

// Setup the localizer by providing the moment (or globalize) Object
// to the correct localizer.
moment.locale(LANGUAGES[0])
const mlocalizer = momentLocalizer(moment)

const messages: CalendarLanguagesMode = {
  vi: {
    showMore: (total: number) => `+${total} ${TASK}`,
  },
}

export const ICalendar = (props: ICalendarProps): JSX.Element => {
  const { events, filterMode, listViews, onChange, onCreateEvent, onNavigate } = props

  const [toggleModal, setToggleModal] = useState<boolean>(false)
  const [eventsMore, setEventsMore] = useState<Event[]>([])
  const [dateMore, setDateMore] = useState<Date>()

  const language = LANGUAGES[0]

  useCalendarModeTracking(filterMode.view || '')

  const onToggleShowMore = (data: any, date: Date): void => {
    setToggleModal(!toggleModal)
    setEventsMore(data)
    setDateMore(date)
  }

  const handleSelectItem = (event: Event): void => {
    localStorage.setItem(
      'view_detail_task_tracking',
      JSON.stringify({
        source: 'calendar',
        mode: filterMode.view || '',
      }),
    )

    window.open(`/calendar-task/detail/${event.resource.id}`, '_blank')
  }

  // sort event with view is not agenda
  if (filterMode.view !== 'agenda') {
    events.sort((a: any, b: any) => {
      return b.resource.priority - a.resource.priority
    })
    events.sort((a: any, b: any) => {
      return a.resource.status - b.resource.status
    })
  }

  return (
    <Typography className="big-calendar" component="div">
      <Calendar
        components={{
          event: MyEvent,
          day: { header: MyCustomHeader },
          week: { header: MyCustomHeader },
          month: { header: MyCustomHeader },
          toolbar: (toolbar) =>
            CustomToolbar({
              mode: filterMode.mode,
              prevView: filterMode.prevView,
              toolbar,
              onHandleNavigate: (data: TNavigate) => onNavigate(data),
            }),
          timeGutterHeader: () => <p>{upperFirst(LABEL_ALL_DAY)}</p>,
        }}
        // culture="vi"
        messages={messages[language]}
        defaultDate={moment(currentTime).toDate()}
        doShowMoreDrillDown={false}
        events={events}
        eventPropGetter={() => {
          return {
            style: {
              background: '#fff',
              color: '#727272',
            },
          }
        }}
        formats={{
          dayHeaderFormat: DAY_HEADER_FORMAT,
          monthHeaderFormat: MONTH_HEADER_FORMAT,
          dayRangeHeaderFormat: (range: { end: Date; start: Date }): string => {
            // for week view
            const formatStart = `Ngy ${moment(getStartDate(range.start, 'day')).format('DD MMMM')}`
            const formatEnd = `Ngy ${moment(getStartDate(range.end, 'day')).format('DD MMMM')}`
            const convertFormatEnd = convertDateText(formatEnd)

            return `${formatStart} - ${convertFormatEnd}`
          },
        }}
        localizer={mlocalizer}
        resourceIdAccessor="id"
        resourceTitleAccessor="name"
        selectable
        tooltipAccessor={() => {
          return ''
        }}
        view={filterMode.view}
        views={listViews}
        onSelectEvent={handleSelectItem}
        onSelectSlot={onCreateEvent}
        onShowMore={(data, date) => onToggleShowMore(data, date)}
        onView={(view) => onChange('view', view)}
      />

      {toggleModal && (
        <Dialog
          className="dialog-container"
          fullWidth
          maxWidth="sm"
          open={toggleModal}
          scroll="body"
          onClose={() => setToggleModal(false)}
        >
          <MyMoreEvent events={eventsMore} date={dateMore} currentMode={filterMode.view} />
        </Dialog>
      )}
    </Typography>
  )
}
