import React, { useContext, useEffect, useState } from 'react'
import { v4 as uuidv4 } from 'uuid'

import { TodoForm } from 'pages/TodoList/TodoForm'

import { IEditTask } from 'modules/EditTask/index.d'

import { RESPONSE_GET_SUCCESS } from 'constants/common'
import { TODO_TASK_UPDATE_DEFAULT_VALUE } from 'constants/defaultValue'

import { IToDoTaskDataPost } from 'utils/form'

import { CommonContext } from 'context/CommonContext'

import { getDetailTask, submitEditTask } from 'services/api/Task'
import taskTrackingService from 'services/taskTrackingService'

import 'modules/EditTask/styles.scss'

const EditTask = (props: IEditTask): JSX.Element => {
  const { setLoading } = useContext(CommonContext)

  const { displayModal, id, title, setDisplayModal } = props
  const [todoTask, setTodoTask] = useState<IToDoTaskDataPost>(TODO_TASK_UPDATE_DEFAULT_VALUE)

  const fetchData = async (): Promise<void> => {
    setLoading(true)
    const resp = await getDetailTask(id)

    setLoading(false)
    if (resp.code === RESPONSE_GET_SUCCESS) {
      // check data exist and length
      if (resp.data) {
        // update options
        setTodoTask(resp.data)
      }
    }
  }
  useEffect(() => {
    fetchData()
  }, [id])

  const handleCloseModal = (modal: boolean, isAddOrUpdate?: boolean): void => {
    // Reset all select child
    setTodoTask(TODO_TASK_UPDATE_DEFAULT_VALUE)

    setDisplayModal(modal, isAddOrUpdate)
  }

  const handleSubmitForm = async (dataPost: IToDoTaskDataPost): Promise<void> => {
    // set Loading
    setLoading(true)

    const resp = await submitEditTask(dataPost)

    setLoading(false)

    alert(resp.message)
    if (resp.code === RESPONSE_GET_SUCCESS) {
      // close modal
      handleCloseModal(displayModal, true)

      taskTrackingService.sendEvent({
        eventName: 'cal_user_edit_task',
        timestamp: +new Date(),
        eventTransactionId: uuidv4(),
      })
    }
  }

  return (
    <TodoForm
      displayModal={displayModal}
      id={id}
      item={todoTask}
      title={title}
      handleCloseModal={handleCloseModal}
      handleSubmitForm={handleSubmitForm}
      setDisplayModal={setDisplayModal}
      setTodoTask={setTodoTask}
    />
  )
}

export default EditTask
