import React, { useCallback, useContext, useEffect, useState, useMemo } from 'react'

import { TAutocomplete } from 'components/TAutocomplete'
import { ISelectPriorityProps } from 'modules/ISelectPriority/index.d'

import { parseOptions, PRIORITY_ICON_OPTION } from 'utils/renderComponent'

import {
  LABEL_PRIORITY,
  RESPONSE_GET_SUCCESS,
  PLACEHOLDER_PRIORITY,
  PLACEHOLDER_PRIORITY_HAS_VALUE,
} from 'constants/common'
import { CommonContext } from 'context/CommonContext'
import { getTaskPriorityList } from 'services/api/GetOptions'
import { IOption } from 'utils/form'
import { isNotEmpty } from 'utils/util'

export const ISelectPriority = (props: ISelectPriorityProps): JSX.Element => {
  const { setLoading } = useContext(CommonContext)
  const { errMsg, multiple, placeholder = PLACEHOLDER_PRIORITY, required, value, onChange } = props

  const itemName = 'priority'

  const memoPlaceholder = useMemo(() => {
    return isNotEmpty(value) ? PLACEHOLDER_PRIORITY_HAS_VALUE : placeholder
  }, [placeholder, value])

  // parse response items
  const [optionItems, setOptionItems] = useState<IOption[]>([])

  let hasData = false
  const fetchData = useCallback(async (): Promise<void> => {
    setLoading(true)
    const resp = await getTaskPriorityList()
    setLoading(false)

    if (!hasData && resp.code === RESPONSE_GET_SUCCESS) {
      // check data exist and length
      if (resp.data.length > 0) {
        hasData = true
        // parse response resp
        let parseData = parseOptions(itemName, resp.data, PRIORITY_ICON_OPTION)
        parseData = optionItems.concat(parseData)

        // update options
        setOptionItems(parseData)
      }
    }
  }, [])
  useEffect(() => {
    fetchData()
  }, [])

  return (
    <TAutocomplete
      error={errMsg}
      id={itemName}
      label={LABEL_PRIORITY}
      multiple={multiple}
      name={itemName}
      option={optionItems}
      placeholder={memoPlaceholder}
      required={required}
      value={value}
      onChange={onChange}
    />
  )
}
