import React, { useCallback, useEffect, useState, useMemo } from 'react'

import isArray from 'lodash/isArray'

import { ISelectDepartmentProps } from 'modules/ISelectDepartment/index.d'
import { TAutocomplete } from 'components/TAutocomplete'

import { getDepartmentList } from 'services/api/GetOptions'

import { parseDepartmentOptions } from 'utils/renderComponent'
import { IOption } from 'utils/form'
import { isNotEmpty } from 'utils/util'

import {
  LABEL_DEPARTMENT,
  RESPONSE_GET_SUCCESS,
  PLACEHOLDER_DEPARTMENT,
  PLACEHOLDER_DEPARTMENT_HAS_VALUE,
} from 'constants/common'

export const ISelectDepartment = (props: ISelectDepartmentProps): JSX.Element => {
  const { errMsg, multiple, placeholder = PLACEHOLDER_DEPARTMENT, required, value, onChange } = props
  const convertToNumber = (isArray(value) && value) || ''

  const itemName = 'departmentIds'

  const memoPlaceholder = useMemo(() => {
    return isNotEmpty(value) ? PLACEHOLDER_DEPARTMENT_HAS_VALUE : placeholder
  }, [placeholder, value])

  const [optionItems, setOptionItems] = useState<IOption[]>([])

  const fetchData = useCallback(async (): Promise<void> => {
    let hasData = false
    const items = await getDepartmentList()

    if (!hasData && items.code === RESPONSE_GET_SUCCESS) {
      // check data exist and length
      if (items.data || (items.data && items.data.length > 0)) {
        hasData = true
        // parse response items
        let parseData = parseDepartmentOptions(itemName, items.data)
        parseData = optionItems.concat(parseData)

        // update options
        setOptionItems(parseData)
      }
    }
  }, [optionItems])

  useEffect(() => {
    fetchData()
  }, [])

  const handleChange = (name: string, val: null | number | number[]): void => {
    if (multiple) {
      onChange(name, val as number[])
    } else {
      onChange(name, val ? [val as number] : [])
    }
  }

  return (
    <TAutocomplete
      error={errMsg}
      id={itemName}
      label={LABEL_DEPARTMENT}
      multiple={multiple}
      name={itemName}
      option={optionItems}
      placeholder={memoPlaceholder}
      required={required}
      value={convertToNumber}
      onChange={handleChange}
    />
  )
}
