import React, { useEffect, useState } from 'react'

import { Button, Dialog, DialogActions, DialogContent, IconButton, Toolbar, Typography } from '@material-ui/core'

import { ICommentForm } from 'components/TCommentForm/index.d'

import { CommentView } from 'my-comment-category/lib'

import { BTN_CLOSE_COMMENT, BTN_SAVE } from 'constants/common'

import { ITaskCommentData } from 'utils/form'
import { Category } from 'model/comment'

import 'components/TCommentForm/styles.scss'

export const TCommentForm = (props: ICommentForm): JSX.Element => {
  const {
    displayModal,
    id,
    title,
    handleSubmitForm,
    handleCloseModal,
    createdBy,
    categoryConfig,
    listCommentSuggestion,
  } = props

  const [errorsConfig, setErrorsConfig] = useState<any>([])

  const [dataPost, setDataPost] = useState<ITaskCommentData>({
    dataId: id,
    comments: [],
    createdBy,
  })

  useEffect(() => {
    const errorsCfg = categoryConfig.map((config) => ({
      requiredInput: config.showTextInput ? !!config.requiredTextInput : false,
      errorInput: false,
      requiredImage: config.showPhoto ? !!config.requiredPhoto : false,
      errorImage: false,
    }))
    setErrorsConfig(errorsCfg)
  }, [categoryConfig])

  const handleSubmitData = (): void => {
    const validation = {
      config: false,
    }
    const errorsConfigAfterCheck = errorsConfig.map((item: any, index: number) => ({
      ...item,
      errorInput: item.requiredInput ? dataPost.comments[index].comments.length === 0 : false,
      errorImage: item.requiredImage ? dataPost.comments[index].photos.length === 0 : false,
    }))
    setErrorsConfig(errorsConfigAfterCheck)

    validation.config = !!errorsConfigAfterCheck.find((item: any) => item.errorInput || item.errorImage)

    if (!validation.config) {
      handleSubmitForm(dataPost)
    }
  }
  const onDataCommentChange = (data: Category[]): void => {
    if (data && data.length) {
      const errorsConfigAfterCheck = errorsConfig.map((item: any, index: number) => ({
        ...item,
        errorInput: data[index].comments.length > 0 ? false : item.errorInput,
        errorImage: data[index].photos.length > 0 ? false : item.errorImage,
      }))
      setErrorsConfig(errorsConfigAfterCheck)
      setDataPost({ ...dataPost, comments: data })
    }
  }

  return (
    <Dialog
      className="comment-input-container"
      fullWidth
      maxWidth="md"
      open={displayModal}
      scroll="body"
      onClose={() => {}}
    >
      <Toolbar className="comment-input-toolbar">
        <Typography className="title" variant="h6">
          {title}
        </Typography>
        <IconButton className="icon-close" onClick={() => handleCloseModal(displayModal)}>
          &times;
        </IconButton>
      </Toolbar>
      <DialogContent className="comment-input-content" dividers>
        <CommentView
          onDataChange={onDataCommentChange}
          categoryConfigData={categoryConfig}
          commentSuggestionResponse={listCommentSuggestion}
          errorsConfig={errorsConfig}
          onCommentPriceTagChange={() => {
            /* Do nothing */
          }}
        />
      </DialogContent>
      <DialogActions className="comment-input-actions">
        <Button autoFocus variant="outlined" onClick={() => handleCloseModal(displayModal)}>
          {BTN_CLOSE_COMMENT}
        </Button>
        <Button autoFocus color="primary" variant="contained" onClick={handleSubmitData}>
          {BTN_SAVE}
        </Button>
      </DialogActions>
    </Dialog>
  )
}
