import React, { useCallback, useEffect, useState } from 'react'

import { IViewRelatedToProps } from 'modules/IViewRelatedTo/index.d'

import { TLabelValue } from 'components/TLabelValue'
import { TTitle } from 'components/TTitle'

import { getRelatedList } from 'services/api/GetOptions'

import { LABEL_RELATED_TO, RESPONSE_GET_SUCCESS } from 'constants/common'
import { RELATED_TYPE_IS_DEAL } from 'constants/defaultValue'
import { renderLienQuanDen } from 'utils/renderComponent'

export const IViewRelatedTo = (props: IViewRelatedToProps): JSX.Element => {
  const { relatedTo, relatedType } = props
  const [data, setData] = useState({
    id: '',
    name: '',
    label: 'Listing',
  })

  let isDeal = false
  if (relatedType === RELATED_TYPE_IS_DEAL) {
    isDeal = true
  }

  const fetchData = useCallback(async (): Promise<void> => {
    const resp = await getRelatedList(isDeal, relatedTo)

    if (resp && resp.code === RESPONSE_GET_SUCCESS) {
      // check data exist and length
      if (Object.keys(resp.data).length > 0) {
        const dataConvert = {
          ...data,
          id: resp.data.id,
          name: resp.data.name,
        }
        if (isDeal) {
          dataConvert.label = 'Deal'
        }
        // update options
        setData(dataConvert)
      }
    }
  }, [])

  useEffect(() => {
    fetchData()
  }, [])

  return (
    <TLabelValue
      className="no-padding"
      label={LABEL_RELATED_TO}
      value={<TTitle className="item-value" title={renderLienQuanDen(data)} />}
    />
  )
}
