import React from 'react'
import { Button } from '@material-ui/core'

import { TButtonProps } from 'components/TButton/index.d'

import 'components/TButton/index.scss'

export const TButton = (props: TButtonProps): JSX.Element => {
  const { className, color, disabled, label, startIcon, variant, onClick } = props

  return (
    <Button className={`def-btn ${className}`} color={color} disabled={disabled} variant={variant} onClick={onClick}>
      {startIcon} {label}
    </Button>
  )
}
