import React, { useCallback, useContext, useEffect, useState, useMemo } from 'react'

import isArray from 'lodash/isArray'

import { CommonContext } from 'context/CommonContext'

import { ISelectZoneProps } from 'modules/ISelectZone/index.d'
import { TAutocomplete } from 'components/TAutocomplete'

import { getZoneList } from 'services/api/GetOptions'

import { LABEL_ZONE, RESPONSE_GET_SUCCESS, PLACEHOLDER_ZONE, PLACEHOLDER_ZONE_HAS_VALUE } from 'constants/common'

import { parseZoneOptions } from 'utils/renderComponent'
import { IOption } from 'utils/form'
import { isNotEmpty } from 'utils/util'

export const ISelectZone = (props: ISelectZoneProps): JSX.Element => {
  const { setLoading } = useContext(CommonContext)

  const { errMsg, multiple, placeholder = PLACEHOLDER_ZONE, required, value, onChange } = props
  const convertToNumber = (isArray(value) && value) || ''

  const itemName = 'zoneIds'

  const memoPlaceholder = useMemo(() => {
    return isNotEmpty(value) ? PLACEHOLDER_ZONE_HAS_VALUE : placeholder
  }, [placeholder, value])

  const [optionItems, setOptionItems] = useState<IOption[]>([])

  const fetchData = useCallback(async (): Promise<void> => {
    setLoading(true)
    const items = await getZoneList()
    setLoading(false)

    if (items.code === RESPONSE_GET_SUCCESS) {
      // check data exist and length
      if (items.data || (items.data && items.data.length > 0)) {
        // parse response items
        const parseData = parseZoneOptions(itemName, items.data)

        // update options
        setOptionItems(parseData)
      }
    }
  }, [optionItems])

  useEffect(() => {
    fetchData()
  }, [])

  const handleChange = (name: string, val: null | number | number[]): void => {
    if (multiple) {
      onChange(name, val as number[])
    } else {
      onChange(name, val ? [val as number] : [])
    }
  }

  return (
    <TAutocomplete
      error={errMsg}
      id={itemName}
      label={LABEL_ZONE}
      multiple={multiple}
      name={itemName}
      option={optionItems}
      placeholder={memoPlaceholder}
      required={required}
      value={convertToNumber}
      onChange={handleChange}
    />
  )
}
