import { stringOrDate } from 'react-big-calendar'

import moment from 'moment'

import { DATE_FORMAT_MAIN, LABEL_ALL_DAY, TIME_FORMAT } from 'constants/common'
import { endDay, endMonth, endWeek, startDay, startMonth, startWeek } from 'constants/defaultValue'

import { TNavigate } from 'utils/renderComponent.d'

export const encode = (component: []): string => {
  return encodeURIComponent(JSON.stringify(component))
}

export const timeoutPromiseResolve = (interval: number): Promise<string> => {
  return new Promise((resolve) => {
    setTimeout(function () {
      resolve('successful')
    }, interval)
  })
}

export type VALIDATION_FIELD = {
  [x: string]: {
    name: string
    label: string
    value: unknown
    rule: {
      email?: {
        maxLength?: number
        minLength?: number
      }
      lessThan?: boolean
      maxLength?: number
      minLength?: number
      required?: boolean
    }
  }
}

export type VALIDATION_RESPONSE = {
  [x: string]: string
}

export const getStartDate = (d: Date | stringOrDate, typeDate: 'day' | 'week' | 'month'): number => {
  return moment(d).startOf(typeDate).unix() * 1000
}
export const getEndDate = (d: Date | stringOrDate, typeDate: 'day' | 'week' | 'month'): number => {
  return moment(d).endOf(typeDate).unix() * 1000
}

export const renderDateFromTo = (props: TNavigate): TNavigate => {
  const { date, mode, prevView, view } = props
  let { fromDate, toDate } = props

  switch (view) {
    case 'day':
      fromDate = startDay
      toDate = endDay
      if (date) {
        fromDate = getStartDate(date, 'day')
        toDate = getEndDate(date, 'day')
      }
      break
    case 'week':
      fromDate = startWeek
      toDate = endWeek
      if (date) {
        fromDate = getStartDate(date, 'week')
        toDate = getEndDate(date, 'week')
      }
      break
    case 'month':
      fromDate = startMonth
      toDate = endMonth
      if (date) {
        fromDate = getStartDate(date, 'month')
        toDate = getEndDate(date, 'month')
      }
      break
    default:
      switch (prevView) {
        case 'week':
          fromDate = startWeek
          toDate = endWeek
          break
        case 'month':
          fromDate = startMonth
          toDate = endMonth
          break
        default:
          fromDate = startDay
          toDate = endDay
          break
      }

      break
  }

  if (mode) {
    switch (prevView) {
      case 'week':
        fromDate = startWeek
        toDate = endWeek
        break
      case 'month':
        fromDate = startMonth
        toDate = endMonth
        break
      default:
        fromDate = startDay
        toDate = endDay
        break
    }
  }

  return {
    fromDate,
    prevView,
    toDate,
    view,
  }
}

export const renderEventTime = (timeFrom: number, timeTo?: number, isAllDay?: boolean, isMoreEvent?: boolean): string => {
  if (isAllDay) {
    if (isMoreEvent) {
      return LABEL_ALL_DAY
    }

    return ''
  }

  let timeFromTo = ``
  const formatTimeFrom = moment(timeFrom).format(TIME_FORMAT)
  if (timeTo) {
    const formatTimeTo = moment(timeTo).format(TIME_FORMAT)

    timeFromTo = `${formatTimeFrom} - ${formatTimeTo}`
  }

  return timeFromTo
}

export function randomInRange(min: number, max: number): number {
  return Math.floor(Math.random() * (max - min + 1)) + min
}

export const getTimestamp = (date: number, time: number, defaultFormat = `${DATE_FORMAT_MAIN} ${TIME_FORMAT}`): number => {
  const getDate = moment(date).format(DATE_FORMAT_MAIN)
  const timeFrom = moment(time).format(TIME_FORMAT)

  return moment(`${getDate} ${timeFrom}`, defaultFormat).unix() * 1000
}

