export const isNotEmpty = (value: any): boolean => {
  if (value && typeof value === 'object') {
    return !!Object.keys(value).length
  }

  if (value === 0) {
    return true
  }

  return !!value
}

export const convertDateText = (label: string): string => {
  const mapDayMonthToVn: any = [
    { 'th': 'Th' },
    { 'thng': 'Thng' }
  ]

  return mapDayMonthToVn.reduce((f: string, s: any) => `${f}`.replace(Object.keys(s)[0], s[Object.keys(s)[0]]), label)
}