import React from 'react'
import { Autocomplete } from '@material-ui/lab'
import { Typography } from '@material-ui/core'

import { ISelectSuggestionFreeTextProps } from 'components/ISelectSuggestionFreeText/index.d'

import { NO_OPTIONS } from 'constants/common'

import 'components/ISelect/index.scss'
import { IOption } from 'utils/form'

export const ISelectSuggestionFreeText = (props: ISelectSuggestionFreeTextProps): JSX.Element => {
  const { id, name, options, value, onInputChange, renderInput } = props

  const getOptionLabel = (option: any): string => {
    if (typeof option === 'object') {
      return option.label
    }

    return option
  }

  const handleInputBlur = (e: any): void | string => {
    if (e) {
      return onInputChange(name, e.target.value)
    }

    return value
  }

  const handleInputChange = (e: any, val: string | IOption | null): void | string => {
    let kw = ''
    if (e) {
      if (val && typeof val === 'object') {
        kw = val.label
      }
      if (typeof val === 'string') {
        kw = val
      }

      return onInputChange(name, kw)
    }

    return value
  }

  return (
    <Autocomplete
      className="autocomplete-container"
      freeSolo
      fullWidth
      getOptionLabel={getOptionLabel}
      id={id}
      noOptionsText={NO_OPTIONS}
      options={options}
      value={value || null}
      onBlur={(event) => handleInputBlur(event)}
      onChange={(event, val) => handleInputChange(event, val)}
      renderInput={renderInput}
      renderOption={(option, { inputValue }) => {
        const parts = option.label.split(new RegExp(`(${inputValue})`, 'gi'))

        return (
          <Typography component="div">
            {parts.map((part: string, i: number) => {
              const key = `highlight-${part}-${i}`
              const clsHighlight = part.toLowerCase() === inputValue.toLowerCase() ? 'highlight' : ''

              return (
                <Typography component="span" key={key} className={clsHighlight}>
                  {part}
                </Typography>
              )
            })}
          </Typography>
        )
      }}
    />
  )
}
