import React from 'react'

import { IViewDescriptionProps } from 'modules/IViewDescription/index.d'

import { LABEL_DESCRIPTION } from 'constants/common'
import { TLabelValue } from 'components/TLabelValue'
import { TTitle } from 'components/TTitle'

export const IViewDescription = (props: IViewDescriptionProps): JSX.Element => {
  const { value } = props

  return (
    <TLabelValue
      className="no-padding"
      label={LABEL_DESCRIPTION}
      value={<TTitle className="item-value" title={<p>{value}</p>} variant="subtitle1" />}
    />
  )
}
