import React, { useCallback, useEffect, useState, useMemo } from 'react'

import { TAutocomplete } from 'components/TAutocomplete'
import { ISelectJobTypeProps } from 'modules/ISelectJobType/index.d'

import { getTypeList } from 'services/api/GetOptions'

import { parseOptions } from 'utils/renderComponent'
import { IOption } from 'utils/form'
import { isNotEmpty } from 'utils/util'

import {
  LABEL_JOB_TYPE,
  RESPONSE_GET_SUCCESS,
  PLACEHOLDER_JOB_TYPE,
  PLACEHOLDER_JOB_TYPE_HAS_VALUE,
} from 'constants/common'

export const ISelectJobType = (props: ISelectJobTypeProps): JSX.Element => {
  const { errMsg, multiple, name = 'type', placeholder = PLACEHOLDER_JOB_TYPE, required, value, onChange } = props

  const memoPlaceholder = useMemo(() => {
    return isNotEmpty(value) ? PLACEHOLDER_JOB_TYPE_HAS_VALUE : placeholder
  }, [placeholder, value])

  const [optionItems, setOptionItems] = useState<IOption[]>([])

  const fetchData = useCallback(async (): Promise<void> => {
    let hasData = false
    const items = await getTypeList()

    if (!hasData && items.code === RESPONSE_GET_SUCCESS) {
      // check data exist and length
      if (items.data || (items.data && items.data.length > 0)) {
        hasData = true
        // parse response items
        let parseData = parseOptions(name, items.data)
        parseData = optionItems.concat(parseData)

        // update options
        setOptionItems(parseData)
      }
    }
  }, [optionItems])

  useEffect(() => {
    fetchData()
  }, [])

  const handleChange = (fieldName: string, val: null | number | number[]): void => {
    if (multiple) {
      onChange(fieldName, val as number[])
    } else {
      onChange(fieldName, val || '')
    }
  }

  return (
    <TAutocomplete
      multiple={multiple}
      error={errMsg}
      id={name}
      label={LABEL_JOB_TYPE}
      name={name}
      option={optionItems}
      placeholder={memoPlaceholder}
      required={required}
      value={value}
      onChange={handleChange}
    />
  )
}
