import { useEffect, useRef, useCallback } from 'react'
import { v4 as uuidv4 } from 'uuid'
import taskTrackingService from 'services/taskTrackingService'
import useLatestValue from './useLatestValue'
import usePreviousValue from './usePreviousValue'

function useCalendarModeTracking(mode: string): void {
  const eventTransactionId = useRef<string | null>(null)
  const prevMode = usePreviousValue(mode)
  const latestMode = useLatestValue(mode)

  const sendEvent = (eventName: string): void => {
    if (!eventTransactionId.current) {
      return
    }

    taskTrackingService.sendEvent({
      eventName,
      timestamp: +new Date(),
      eventTransactionId: eventTransactionId.current,
    })
  }

  const handleModeChange = async (): Promise<void> => {
    if (prevMode && eventTransactionId.current) {
      sendEvent(`cal_user_end_view_calendar_${prevMode}`)
    }

    eventTransactionId.current = uuidv4()

    sendEvent(`cal_user_start_view_calendar_${mode}`)
  }

  const handleVisibilitychange = useCallback(() => {
    if (document.visibilityState === 'hidden') {
      sendEvent(`cal_user_end_view_calendar_${latestMode.current}`)
    } else {
      sendEvent(`cal_user_start_view_calendar_${latestMode.current}`)
    }
  }, [])

  useEffect(() => {
    handleModeChange()
  }, [mode])

  useEffect(() => {
    taskTrackingService.connect()
    document.addEventListener('visibilitychange', handleVisibilitychange)

    return () => {
      taskTrackingService.disconnect()
      document.removeEventListener('visibilitychange', handleVisibilitychange)
    }
  }, [])
}

export default useCalendarModeTracking
