import React from 'react'

import { Typography } from '@material-ui/core'

import { ILoadingProps } from 'components/ILoading/index.d'

import 'components/ILoading/index.scss'

export const ILoading = (props: ILoadingProps): JSX.Element => {
  const { status } = props

  let clsLoading = 'iloading'
  if (status) {
    clsLoading += ' active'
  }

  return (
    <Typography className={clsLoading} component="div">
      <img src={`${process.env.REACT_APP_PUBLIC_IMAGE_PATH}/loading.svg`} alt="Loading" />
    </Typography>
  )
}
