import React, { FC, useCallback, useContext, useEffect, useState } from 'react'

import { FormHelperText, Grid, Typography } from '@material-ui/core'

import { TSearchBar } from 'modules/SearchBar/index.d'
import { TSelectWithNumberFormat } from 'modules/TSelectWithNumberFormat'

import { ISelectSuggestion } from 'components/ISelectSuggestion'

import { LABEL_RELATED_TO, RESPONSE_GET_SUCCESS, SUGGESTION_LIMIT } from 'constants/common'
import { RELATED_TYPE_IS_DEAL } from 'constants/defaultValue'
import { DefaultData } from 'constants/mockupData'

import { CommonContext } from 'context/CommonContext'

import { IOption } from 'utils/form'
import { parseOptions, parseRelatedDealOptions, parseRelatedListingOptions } from 'utils/renderComponent'
import { getRelatedDealListingList, getRelatedTypeList } from 'services/api/GetOptions'

import 'modules/SearchBar/styles.scss'

export const SearchBar: FC<TSearchBar> = (props) => {
  const { setLoading } = useContext(CommonContext)
  const { assignedTo, errMsg, relatedTo, relatedType, onChangeRelatedTo, onChangeRelatedType } = props

  const itemRelatedTypeName = 'relatedType'
  // parse response items
  const parseRelatedType = parseOptions(itemRelatedTypeName, [DefaultData])
  const [optionsRelatedType, setOptionsRelatedType] = useState<IOption[]>(parseRelatedType)

  const itemName = 'relatedTo'
  const [optionItems, setOptionItems] = useState<IOption[]>([])

  const [inputValue, setInputValue] = useState<string | undefined>('')
  const handleInputChange = (name: string, val: string): void => {
    setInputValue(val)
  }

  const fetchData = useCallback(async (): Promise<void> => {
    if (!relatedType || !assignedTo) {
      setOptionItems([])
      onChangeRelatedTo(itemName, undefined)
      return
    }

    let kw: number | string | undefined = inputValue
    if (relatedTo) {
      kw = relatedTo
    }

    let isDeal = false
    let fn = parseRelatedListingOptions
    if (relatedType === RELATED_TYPE_IS_DEAL) {
      isDeal = true
      fn = parseRelatedDealOptions
    }
    const dataPost = {
      assignedTo,
      page: 0,
      search: kw,
      size: SUGGESTION_LIMIT,
    }

    setLoading(true)
    const resp = await getRelatedDealListingList(isDeal, dataPost)
    setLoading(false)

    let parseData: IOption[] = []
    if (resp.code === RESPONSE_GET_SUCCESS && resp.data.totalElements > 0) {
      // check data exist and length
      if (resp.data.content.length > 0) {
        // parse response items
        parseData = fn(itemName, resp.data.content)
      }
    }

    // update options
    setOptionItems(parseData)
  }, [assignedTo, inputValue, relatedType])
  useEffect(() => {
    fetchData()
  }, [assignedTo, inputValue, relatedType])

  const fetchRelatedType = useCallback(async (): Promise<void> => {
    if (!assignedTo) {
      setOptionsRelatedType(optionsRelatedType)
      return
    }
    setLoading(true)
    const respRelatedType = await getRelatedTypeList(assignedTo)
    setLoading(false)

    // check data exist and length
    let relatedTypeOptions: IOption[] = parseRelatedType
    let getRalatedType: number | string = ''
    if (respRelatedType.code === RESPONSE_GET_SUCCESS && respRelatedType.data && respRelatedType.data.length > 0) {
      // parse response items
      relatedTypeOptions = parseOptions(itemRelatedTypeName, respRelatedType.data)
      // update options
      setOptionsRelatedType(relatedTypeOptions)

      getRalatedType = relatedTypeOptions[0].value
    }

    setOptionsRelatedType(relatedTypeOptions)
    onChangeRelatedType(itemRelatedTypeName, getRalatedType)
  }, [assignedTo])
  useEffect(() => {
    fetchRelatedType()
  }, [assignedTo])

  return (
    <Typography className="search-bar" component="div">
      <Typography className="required" variant="subtitle2" component="p">
        {LABEL_RELATED_TO}
      </Typography>
      <Grid container>
        <Grid item xs={3}>
          <TSelectWithNumberFormat
            className="style-background-grey"
            id={itemRelatedTypeName}
            name={itemRelatedTypeName}
            options={optionsRelatedType}
            value={relatedType}
            onChange={onChangeRelatedType}
          />
        </Grid>
        <Grid item xs={9}>
          <ISelectSuggestion
            id={itemName}
            name={itemName}
            options={optionItems}
            value={relatedTo}
            onChange={onChangeRelatedTo}
            onInputChange={(name, val) => handleInputChange(name, val)}
          />

          <FormHelperText id="component-error-text">{errMsg}</FormHelperText>
        </Grid>
      </Grid>
    </Typography>
  )
}
