import * as api from 'services/api/BaseApi'
import { FullResponse, ITodoResponse } from 'services/api/api.d'

import { ITaskCanceledPost, ITaskCompletedPost, IToDoTaskDataPost } from 'utils/form'
import { IFilterHeaderValues } from 'modules/IFilterHeader/index.d'
import { CategoryConfigResponse, CommentSuggestionResponse } from 'model/comment'
import { BASE_API, BACKOFFICE_API, PAGINATION_LIMIT } from 'constants/common'

export const uploadFile = (fileName: FormData): Promise<FullResponse<any>> => {
  return api.post({
    embbedHeader: true,
    url: `${BASE_API}file/upload`,
    payload: fileName,
    isUpload: true,
  })
}

export const submitCreateTask = (dataPost: IToDoTaskDataPost): Promise<FullResponse<any>> => {
  return api.post({ embbedHeader: true, url: `${BASE_API}task`, payload: dataPost })
}

export const submitEditTask = (dataPost: IToDoTaskDataPost): Promise<FullResponse<any>> => {
  return api.put({ embbedHeader: true, url: `${BASE_API}task`, payload: dataPost })
}

export const getDetailTask = (id: number): Promise<FullResponse<any>> => {
  return api.get({ url: `${BASE_API}task/${id}`, embbedHeader: true })
}

export const submitCompletedTask = (dataPost: ITaskCompletedPost): Promise<FullResponse<any>> => {
  return api.post({
    embbedHeader: true,
    url: `${BASE_API}task/complete`,
    payload: dataPost,
  })
}

export const submitCanceledTask = (dataPost: ITaskCanceledPost): Promise<FullResponse<any>> => {
  return api.post({
    embbedHeader: true,
    url: `${BASE_API}task/cancel`,
    payload: dataPost,
  })
}

export const getCommentCategoryConfig = (featureCode: string): Promise<FullResponse<CategoryConfigResponse>> => {
  return api.get({
    url: `${BACKOFFICE_API}get-comment-category-config/${featureCode}`,
    embbedHeader: true,
  })
}

export const getCommentCategorySuggestions = (
  featureCode: string,
): Promise<FullResponse<CommentSuggestionResponse[]>> => {
  return api.get({
    url: `${BACKOFFICE_API}get-comment-suggestions/${featureCode}`,
    embbedHeader: true,
  })
}

export const getCommentByTaskId = (taskId: number): Promise<FullResponse<CommentSuggestionResponse[]>> => {
  return api.get({
    url: `${BACKOFFICE_API}get-comment-category/${taskId}`,
    embbedHeader: true,
  })
}

export const getEventWithFilter = (dataPost: IFilterHeaderValues): Promise<FullResponse<any>> => {
  return api.post({ embbedHeader: true, url: `${BASE_API}task/list`, payload: dataPost })
}
export const getEventWithAgendaFilter = (
  dataPost: IFilterHeaderValues,
  page = 0,
  numberItem = PAGINATION_LIMIT,
): Promise<FullResponse<any>> => {
  return api.post({ embbedHeader: true, url: `${BASE_API}task/agenda/${page}/${numberItem}`, payload: dataPost })
}

export const getPemissionOnAction = (act: string): Promise<ITodoResponse<any>> => {
  return api.get({ embbedHeader: true, url: `${BASE_API}task/checkPermission/${act}` })
}
