import React, { useCallback, useContext, useEffect, useState } from 'react'

import { Typography } from '@material-ui/core'

import { Event, SlotInfo } from 'react-big-calendar'

import { cloneDeep } from 'lodash'

import moment from 'moment'

import { Range } from 'react-date-range'

import { TButton } from 'components/TButton'

import CreateTask from 'modules/CreateTask'
import { IFilterHeader } from 'modules/IFilterHeader'
import { ICalendar } from 'modules/ICalendar'
import { AgendaView } from 'modules/IAgendaGroup/CustomAgenda'
import { IFilterHeaderValues } from 'modules/IFilterHeader/index.d'
import { IFilterMode, TListViews } from 'modules/ICalendar/index.d'

import { CommonContext } from 'context/CommonContext'

import { BTN_LOAD_MORE, RESPONSE_GET_SUCCESS, TITLE_CREATE_TASK } from 'constants/common'
import { DEFAULT_VIEWS, TODO_TASK_FILTER_DEFAULT_VALUE, TODO_TASK_UPDATE_DEFAULT_VALUE } from 'constants/defaultValue'

import { getEventWithAgendaFilter, getEventWithFilter } from 'services/api/Task'

import { useFormControls } from 'utils/useFormControls'
import { parseCalendarItem } from 'utils/renderComponent'
import { renderDateFromTo } from 'utils/util'
import { TNavigate } from 'utils/renderComponent.d'

import 'pages/TodoList/index.scss'
import useLatestValue from 'utils/useLatestValue'

export const TodoList = (): JSX.Element => {
  const { currentUserId, checkPermission, setLoading } = useContext(CommonContext)

  const initialCreateTaskData = cloneDeep(TODO_TASK_UPDATE_DEFAULT_VALUE)
  initialCreateTaskData.assignedTo = currentUserId

  const [displayModal, setDisplayModal] = useState<boolean>(false)
  const [listViews, setListViews] = useState<TListViews | undefined>(DEFAULT_VIEWS)
  const [events, setEvents] = useState<Event[]>([])
  const [assignedToIds, setAssignedToIds] = useState<number[]>([])
  const [defaultCreateTaskData, setDefaultCreateTaskData] = useState(cloneDeep(initialCreateTaskData))

  const [agendaPage, setAgendaPage] = useState<number>(0)
  const [toggleLoadMore, setToggleLoadMore] = useState<boolean>(false)

  const latestEventsValue = useLatestValue(events)

  const defaultFilterData = {
    ...TODO_TASK_FILTER_DEFAULT_VALUE,
    status: [1], // Set status value = "Cha hon tt" on page load
  }

  const { handleInputValue, handleListValue, handleReset, values } = useFormControls(defaultFilterData, {})

  // reset default time date after close modal
  useEffect(() => {
    return () => {
      if (displayModal) {
        setDefaultCreateTaskData(cloneDeep(initialCreateTaskData))
        handleReset(TODO_TASK_FILTER_DEFAULT_VALUE)
      }
    }
  }, [displayModal])

  const fetchData = useCallback(
    async (data?: IFilterHeaderValues | IFilterMode, page = 0): Promise<void | JSX.Element> => {
      // set Loading
      setLoading(true)
      const getPermission = await checkPermission('list')
      if (getPermission.code !== RESPONSE_GET_SUCCESS) {
        alert(getPermission.message)
        window.location.href = '/401-not-permission'

        return
      }

      let dataPost = cloneDeep(values)
      // BE update, not to use 3 fields
      delete dataPost.departmentIds
      delete dataPost.teamIds
      delete dataPost.zoneIds
      delete dataPost.assignedToIdsToDataPost
      if (dataPost.assignedToIds.length === 0) {
        dataPost.assignedToIds = values.assignedToIdsToDataPost
      }

      if (data) {
        dataPost = {
          ...dataPost,
          ...data,
        }

        // set fromDate / toDate
        values.fromDate = dataPost.fromDate
        values.toDate = dataPost.toDate
        if (dataPost.view === 'agenda') {
          values.mode = dataPost.view
        }
        values.view = dataPost.view
        handleListValue(values)
      }

      let getData: Event[] = []
      let items = null
      if (dataPost.view === 'agenda') {
        items = await getEventWithAgendaFilter(dataPost, page)

        setLoading(false)
        if (items.code === RESPONSE_GET_SUCCESS) {
          // check data exist and length
          if (items.data && items.data.content.length > 0) {
            // update options
            getData = parseCalendarItem(items.data.content, values.groupBy, dataPost.fromDate, dataPost.toDate)

            if (page > 0 && latestEventsValue) {
              getData = latestEventsValue.current.concat(getData)
            }
          }

          let toggleLoadMoreBtn = false
          if (page < items.data.totalPages - 1 && items.data.totalPages > 0) {
            toggleLoadMoreBtn = true
          }
          setToggleLoadMore(toggleLoadMoreBtn)
        }
      } else {
        items = await getEventWithFilter(dataPost)

        setLoading(false)
        if (items.code === RESPONSE_GET_SUCCESS) {
          // check data exist and length
          if (items.data && items.data.length > 0) {
            // update options
            getData = parseCalendarItem(items.data, values.groupBy, dataPost.fromDate, dataPost.toDate)
          }
        }
      }

      setEvents(getData)
    },
    [values],
  )
  useEffect(() => {
    fetchData()
  }, [])

  // reset create form data
  const toggleModal = async (isOpen: boolean, isAddOrUpdate?: boolean): Promise<void> => {
    if (isOpen) {
      // set Loading
      setLoading(true)
      const getPermission = await checkPermission('add')
      setLoading(false)

      if (getPermission.code !== RESPONSE_GET_SUCCESS) {
        alert(getPermission.message)

        return
      }
    }
    if (isAddOrUpdate) {
      await fetchData()
    }

    setDisplayModal(isOpen)
  }

  useEffect(() => {
    values.assignedToIdsToDataPost = assignedToIds
    values.assignedToIds = []

    handleListValue(values)
  }, [assignedToIds])

  const onCancelFilter = (): void => {
    handleReset(TODO_TASK_FILTER_DEFAULT_VALUE)
  }

  const onChangeFilter = (name: string, value: any): void => {
    if (value === 'agenda') {
      values.prevView = values.view
      handleInputValue('prevView', values.view)
    }

    // reset previous selected team
    if (name === 'zoneIds') {
      values.teamIds = []
      handleInputValue('teamIds', values.teamIds)
    }

    handleInputValue(name, value)
  }

  const onApplyAdvanceFilter = async (view: string, mode?: string | number): Promise<void> => {
    let getMode = mode
    let getView = view

    // filter by groupBy
    let { groupBy } = values
    let getFromDate
    let getToDate
    if (mode === 'assignee' || mode === 'date') { // 
      // set fromDate / toDate
      const { fromDate, toDate } = renderDateFromTo({ mode, prevView: values.prevView, view })
      getFromDate = fromDate
      getToDate = toDate
      groupBy = mode
      getView = 'agenda'

      values.view = getView
      values.groupBy = groupBy
      values.fromDate = getFromDate
      values.toDate = getToDate
      handleListValue(values)
    } else {
      // set fromDate / toDate
      const { fromDate, toDate } = renderDateFromTo({ prevView: values.view, view })
      getFromDate = fromDate
      getToDate = toDate

      // filter by agenda
      if (view === 'agenda') {
        values.prevView = values.view
        handleListValue(values)

        setListViews({
          ...listViews,
          agenda: AgendaView,
        })
      }

      // reset page, if switch naviage mode
      setAgendaPage(0)

      if (view === 'day' || view === 'week' || view === 'month') {
        getMode = 'list'
        values.mode = getMode
      }
    }

    const dataPost = {
      fromDate: getFromDate,
      groupBy,
      mode: getMode,
      toDate: getToDate,
      view: getView,
    }

    await fetchData(dataPost)
  }

  const onHandleFilter = async (): Promise<void> => {
    await fetchData()
  }

  const onNavigate = async (props: TNavigate): Promise<void> => {
    const dataPost = {
      fromDate: props.fromDate,
      toDate: props.toDate,
    }

    await fetchData(dataPost)
  }

  const onCreateEvent = async (slotInfo: SlotInfo): Promise<void> => {
    // set Loading
    setLoading(true)
    const getPermission = await checkPermission('add')
    if (getPermission.code !== RESPONSE_GET_SUCCESS) {
      alert(getPermission.message)

      return
    }

    setDefaultCreateTaskData((prev) => ({
      ...prev,
      assignedTo: currentUserId,
      completionTimeDate: moment(slotInfo.start).unix() * 1000,
      completionTimeFrom: moment(slotInfo.start).unix() * 1000,
      completionTimeTo: moment(slotInfo.end).unix() * 1000,
    }))
    setDisplayModal(true)
  }

  const onChangeDateAgenda = async (range: Range): Promise<void> => {
    values.fromDate = moment(range.startDate).startOf('day').unix() * 1000
    values.toDate = moment(range.endDate).endOf('day').unix() * 1000

    await fetchData(values)
  }

  const onSetLoadMore = (page: number): void => {
    setAgendaPage(page)

    fetchData(values, page)
  }

  return (
    <>
      <Typography className="calendar-filter" component="div">
        <IFilterHeader
          toggleLoadMore={toggleLoadMore}
          values={values}
          onApplyAdvanceFilter={onApplyAdvanceFilter}
          onCancel={onCancelFilter}
          onChange={onChangeFilter}
          onChangeDateAgenda={onChangeDateAgenda}
          onReset={handleReset}
          onSearch={onHandleFilter}
          onToggleModal={toggleModal}
          setAssignedToIds={setAssignedToIds}
        />
        <ICalendar
          events={events}
          filterMode={values}
          listViews={listViews}
          onChange={onChangeFilter}
          onCreateEvent={onCreateEvent}
          onNavigate={onNavigate}
        />
        {values.view === 'agenda' && toggleLoadMore && (<TButton
          className="btn-load-more"
          color="primary"
          label={BTN_LOAD_MORE}
          variant="outlined"
          onClick={() => onSetLoadMore(agendaPage + 1)}
        />)}
      </Typography>

      <CreateTask
        dataPost={defaultCreateTaskData}
        displayModal={displayModal}
        title={TITLE_CREATE_TASK}
        handleListValue={handleListValue}
        setDisplayModal={(status, isAddOrUpdate) => toggleModal(!status, isAddOrUpdate)}
      />
    </>
  )
}
