import { createContext } from 'react'

type TCommonContext = {
  currentUserId: number
  loading: boolean
  respStatus: boolean
  // eslint-disable-next-line no-unused-vars
  checkPermission: (act: string) => any
  // eslint-disable-next-line no-unused-vars
  setLoading: (status: boolean) => any
  // eslint-disable-next-line no-unused-vars
  setRespStatus: (status: boolean) => any
}
export const CommonContext = createContext<TCommonContext>({
  currentUserId: 159,
  loading: false,
  respStatus: false,
  // eslint-disable-next-line no-unused-vars
  checkPermission: (act: string) => {},
  // eslint-disable-next-line no-unused-vars
  setLoading: (status: boolean) => {},
  // eslint-disable-next-line no-unused-vars
  setRespStatus: (status: boolean) => {},
})
