import React, { useEffect, useState } from 'react'
import { Autocomplete } from '@material-ui/lab'
import { InputAdornment, TextField, Typography } from '@material-ui/core'
import SearchIcon from '@material-ui/icons/Search'

import { ISelectSuggestionProps } from 'components/ISelectSuggestion/index.d'

import { LABEL_SUGGESTION, NO_OPTIONS } from 'constants/common'
import { IOption } from 'utils/form'

import 'components/ISelect/index.scss'

export const ISelectSuggestion = (props: ISelectSuggestionProps): JSX.Element => {
  const { id, name, value, options, onChange, onInputChange } = props
  const [getRelatedValue, setGetRalatedValue] = useState<IOption>()

  useEffect(() => {
    let getValue
    if (value) {
      options.map((opt) => {
        if (Number(opt.value) === value) {
          getValue = opt
        }

        return opt
      })
    }
    setGetRalatedValue(getValue)
  }, [value, options])

  let selectedOpt: IOption

  const getOptionLabel = (option: any): string => {
    if (typeof option === 'object') {
      return option.label
    }

    return ''
  }

  const handleInputChange = (e: any, val: any): void => {
    let kw = val

    // while fill text to search
    if (e) {
      // while press enter key
      if (e.code === 'Enter') {
        const listOpts = options.filter((opt) => opt.label.indexOf(e.target.value) > -1)

        if (selectedOpt) {
          kw = selectedOpt.value?.toString()
        } else {
          kw = listOpts[0] && listOpts[0].label || kw
        }
      }
      if (!val) {
        kw = val
      }
    }

    // on select suggestion
    if (val && typeof val === 'object') {
      kw = val.label
    }

    // debounceFunc(setSearchTerm(kw), 800)
    onInputChange(name, kw)
  }

  const getOptionSelected = (opt: IOption): boolean => {
    return Number(opt.value) === value
  }

  return (
    <Autocomplete
      className="autocomplete-container"
      fullWidth
      getOptionLabel={getOptionLabel}
      id={id}
      // inputValue={getRelatedValue?.label || ''}
      getOptionSelected={getOptionSelected}
      noOptionsText={NO_OPTIONS}
      options={options}
      value={getRelatedValue || null}
      onChange={(event, val) => onChange(name, Number(val?.value))}
      renderInput={(params) => (
        <TextField
          // eslint-disable-next-line react/jsx-props-no-spreading
          {...params}
          className="input-content"
          margin="dense"
          placeholder={LABEL_SUGGESTION}
          variant="outlined"
          InputProps={{
            ...params.InputProps,
            startAdornment: (
              <InputAdornment component="div" position="start">
                <SearchIcon className="icon-style" />
              </InputAdornment>
            ),
          }}
          onChange={e => handleInputChange(e, e.currentTarget.value)}
        />
      )}
      renderOption={(option, { inputValue }) => {
        if (typeof option !== 'object') {
          return option
        }

        const parts = option.label.split(new RegExp(`(${inputValue})`, 'gi'))

        return (
          <Typography component="div">
            {parts.map((part: string, i: number) => {
              const key = `highlight-${part}-${i}`
              const clsHighlight = part.toLowerCase() === inputValue.toLowerCase() ? 'highlight' : ''

              return (
                <Typography component="span" key={key} className={clsHighlight}>
                  {part}
                </Typography>
              )
            })}
          </Typography>
        )
      }}
    />
  )
}
