import React from 'react'

import { Typography } from '@material-ui/core'

import { TTitleProps } from 'components/TTitle/index.d'

export const TTitle = (props: TTitleProps): JSX.Element => {
  const { className, title, variant } = props

  return (
    <Typography component="div" className={`title ${className}`} variant={variant}>
      {title}
    </Typography>
  )
}
