import React from 'react'
import moment from 'moment'

import { IViewCompletedTimeProps } from 'modules/IViewCompletedTime/index.d'

import { TLabelValue } from 'components/TLabelValue'

import { DATE_FORMAT_MAIN, LABEL_COMPLETED_TIME, TIME_FORMAT } from 'constants/common'

import { renderDateTime } from 'utils/renderComponent'
import { currentTime } from 'constants/defaultValue'

export const IViewCompletedTime = (props: IViewCompletedTimeProps): JSX.Element => {
  const { isAllDay, timeDate, timeFrom, timeTo } = props
  let classIsThePast = ''
  // if selected allday
  if (timeFrom && timeFrom < currentTime) {
    classIsThePast = 'is-the-past'
  }

  const formatDate = moment(timeDate).format(DATE_FORMAT_MAIN)
  let formatTimeFrom = null
  let formatTimeTo = null

  let displayDateTime = formatDate
  if (!isAllDay) {
    if (timeFrom && timeFrom < currentTime) {
      classIsThePast = 'is-the-past'
    }


    formatTimeFrom = moment(timeFrom).format(TIME_FORMAT)
    formatTimeTo = moment(timeTo).format(TIME_FORMAT)

    displayDateTime = `${formatDate} ${formatTimeFrom} - ${formatTimeTo}`
  }

  return <TLabelValue className={`no-padding ${classIsThePast}`} label={LABEL_COMPLETED_TIME} value={renderDateTime(displayDateTime)} />
}
