import React, { useState } from 'react'

import { Grid, Typography } from '@material-ui/core'

import { IFilterHeaderProps } from 'modules/IFilterHeader/index.d'

import { ISelectJobType } from 'modules/ISelectJobType'
import { ISelectPriority } from 'modules/ISelectPriority'
import { ISelectStatus } from 'modules/ISelectStatus'
import { ISelectTeam } from 'modules/ISelectTeam'
import { ISelectZone } from 'modules/ISelectZone'
import { ISelectDepartment } from 'modules/ISelectDepartment'
import { ISelectAssignee } from 'modules/ISelectAssignee'

import { IFilterAdvance } from 'modules/IFilterAdvance'

import 'modules/IFilterHeader/index.scss'

import { TButton } from 'components/TButton'

export const IFilterHeader = (props: IFilterHeaderProps): JSX.Element => {
  const {
    values,
    onApplyAdvanceFilter,
    onCancel,
    onChange,
    onChangeDateAgenda,
    onReset,
    onSearch,
    onToggleModal,
    setAssignedToIds
  } = props

  const [toggleHeaderFilter, setToggleHeaderFilter] = useState<boolean>(false)

  let classFilter = ''
  let showStatus = 'hide'
  if (toggleHeaderFilter) {
    classFilter = 'hide'
    showStatus = ''
  }

  const handleCancelFilter = (): void => {
    return onCancel()
  }

  return (
    <Grid className="filter-container" container>
      <Grid item xs={12}>
        <Grid container spacing={2}>
          <Grid item xs={9}>
            <Typography className="filter-tool" component="div">
              <Typography component="h3">Cng c tm kim</Typography>
              <Typography
                className={`toggle-header ${classFilter}`}
                component="span"
                onClick={() => setToggleHeaderFilter(true)}
              >
                n
              </Typography>
              <Typography
                className={`toggle-header ${showStatus}`}
                component="span"
                onClick={() => setToggleHeaderFilter(false)}
              >
                Hin
              </Typography>
            </Typography>
          </Grid>
          <Grid className={`action-search-group ${classFilter}`} item xs={3}>
            <TButton className="search-btn" color="primary" variant="contained" label="Tm kim" onClick={onSearch} />
            <TButton className="cancel-btn" variant="outlined" label="B lc" onClick={handleCancelFilter} />
          </Grid>
        </Grid>
      </Grid>
      <Grid className="filter-header" item xs={12}>
        <Grid className={classFilter} container spacing={2}>
          <Grid item xs={6}>
            <ISelectJobType multiple name="types" value={values.types} onChange={onChange} />
          </Grid>
          <Grid item xs={3}>
            <ISelectStatus multiple value={values.status} onChange={onChange} />
          </Grid>
          <Grid item xs={3}>
            <ISelectPriority value={values.priority} onChange={onChange} />
          </Grid>
          <Grid item xs={3}>
            <ISelectZone value={values.zoneIds} onChange={onChange} />
          </Grid>
          <Grid item xs={3}>
            <ISelectTeam value={values.teamIds} zoneIds={values.zoneIds} onChange={onChange} />
          </Grid>
          <Grid item xs={3}>
            <ISelectDepartment value={values.departmentIds} onChange={onChange} />
          </Grid>
          <Grid item xs={3}>
            <ISelectAssignee departmentIds={values.departmentIds} multiple name="assignedToIds" teamIds={values.teamIds} value={values.assignedToIds} zoneIds={values.zoneIds} onChange={onChange} setAssignedToIds={setAssignedToIds} />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <IFilterAdvance
          values={values}
          onChange={onApplyAdvanceFilter}
          onChangeAgenda={onChangeDateAgenda}
          onChangeFilter={onChange}
          onReset={onReset}
          onToggleModal={onToggleModal}
        />
      </Grid>
    </Grid>
  )
}
