import React from 'react'
import { Typography } from '@material-ui/core'

import { TLabelIconProps } from 'components/TLabelIcon/index.d'

import 'components/TLabelIcon/index.scss'

export const TLabelIcon = (props: TLabelIconProps): JSX.Element => {
  const { className, icon, label, variant } = props

  return (
    <Typography className={`label-icon-container ${className}`} component="div">
      <img alt={label} className="style-icon" src={icon} />
      <Typography className="style-icon-label" variant={variant}>
        {label}
      </Typography>
    </Typography>
  )
}
