import { BASE_API, PAGINATION_LIMIT } from 'constants/common'

import * as api from 'services/api/BaseApi'
import { FullResponse, ITodoResponse } from 'services/api/api'

import {
  TAssigneeToResp,
  TDataPost,
  TFilterAssignee,
  TPriorityResp,
  TRelatedDealListingResp,
  TRelatedToResponse,
  TRelatedTypeResp,
  TStatusResp,
  TTypeListResp,
} from 'services/api/GetOptions.d'

export const getTypeList = (): Promise<FullResponse<TTypeListResp[]>> => {
  return api.get({ url: `${BASE_API}task/getTaskTypeList`, embbedHeader: true })
}

export const getAssigneeToList = (): Promise<FullResponse<TAssigneeToResp[]>> => {
  return api.get({ url: `${BASE_API}task/getUserList`, embbedHeader: true, payload: null })
}

export const getTaskPriorityList = (): Promise<FullResponse<TPriorityResp[]>> => {
  return api.get({ url: `${BASE_API}task/getTaskPriorityList`, embbedHeader: true })
}
export const getRelatedTypeList = (assignedTo?: number): Promise<ITodoResponse<TRelatedTypeResp[]>> => {
  return api.get({ url: `${BASE_API}task/getRelatedTypeList/${assignedTo}`, embbedHeader: true })
}

export const getLocationTcList = (): Promise<FullResponse<TAssigneeToResp[]>> => {
  return api.get({
    url: `${BASE_API}task/getTcList/1/${PAGINATION_LIMIT}`,
    embbedHeader: true,
  })
}

export const getRelatedDealListingList = (
  isDeal: boolean,
  dataPost: TDataPost,
): Promise<FullResponse<TRelatedDealListingResp[]>> => {
  let linkApi = 'getListingRelatedList'
  if (isDeal) {
    linkApi = 'getDealRelatedList'
  }

  return api.post({
    url: `${BASE_API}task/${linkApi}`,
    embbedHeader: true,
    payload: dataPost,
  })
}

export const getStatusList = (): Promise<FullResponse<TStatusResp[]>> => {
  return api.get({ url: `${BASE_API}task/getTaskStatusList`, embbedHeader: true })
}

export const getReminderList = (): Promise<FullResponse<TStatusResp[]>> => {
  return api.get({ url: `${BASE_API}task/getTaskReminderList`, embbedHeader: true })
}

export const getRelatedList = (isDeal: boolean, id?: number | string): Promise<FullResponse<TRelatedToResponse>> => {
  let linkApi = 'getListingRelatedList'
  if (isDeal) {
    linkApi = 'getDealRelatedList'
  }

  return api.get({
    url: `${BASE_API}task/${linkApi}/${id}`,
    embbedHeader: true,
  })
}

export const getZoneList = (): Promise<FullResponse<TStatusResp[]>> => {
  return api.get({ url: `${BASE_API}task/getZoneList`, embbedHeader: true })
}

export const getTeamList = (zoneIds?: number[]): Promise<FullResponse<TStatusResp[]>> => {
  return api.post({ url: `${BASE_API}task/getTeamList`, embbedHeader: true, payload: { zoneIds } })
}

export const getDepartmentList = (): Promise<FullResponse<TStatusResp[]>> => {
  return api.get({ url: `${BASE_API}task/getDepartmentList`, embbedHeader: true })
}

export const getUserListFiltered = (dataPost: TFilterAssignee): Promise<FullResponse<TAssigneeToResp[]>> => {
  return api.post({ url: `${BASE_API}task/getUserListFiltered`, embbedHeader: true, payload: dataPost })
}
