import React, { memo, useEffect } from 'react'

import { Button, Dialog, DialogActions, DialogContent, Grid, IconButton, Toolbar, Typography } from '@material-ui/core'

import moment from 'moment'
import cloneDeep from 'lodash/cloneDeep'

import { ITodoForm } from 'pages/TodoList/index.d'

import { ISelectJobType } from 'modules/ISelectJobType'
import { ISelectAssignee } from 'modules/ISelectAssignee'
import { ISelectPriority } from 'modules/ISelectPriority'
import Attachment from 'modules/Attachment'
import { SearchBar } from 'modules/SearchBar'
import { CompletedTime } from 'modules/CompletedTime'
import { ISelectRemider } from 'modules/ISelectRemider'
import { SelectLocation } from 'modules/SelectLocation'

import { TextInput } from 'components/TextInput'

import { useFormControls } from 'utils/useFormControls'
import { AttachedFile } from 'utils/form'

import {
  BTN_CANCEL,
  BTN_SAVE,
  ERROR_TIME_IN_THE_PAST,
  LABEL_DESCRIPTION,
  LABEL_JOB_NAME,
  MAX_LENGTH_INPUT,
  PLACEHOLDER_ASSIGNEE_SINGLE,
  PLACEHOLDER_JOB_NAME,
  PLACEHOLDER_JOB_TYPE_SINGLE,
  PLACEHOLDER_PRIORITY_SINGLE,
  TIME_FORMAT,
  validationFields,
} from 'constants/common'
import { currentTime } from 'constants/defaultValue'

export const TodoForm = memo(
  (props: ITodoForm): JSX.Element => {
    // State
    const { displayModal, id, item, title, handleCloseModal, handleSubmitForm } = props

    const {
      handleInputValue,
      handleReset,
      // handleListValue,
      // handleResetValidationFields,
      // handleFormSubmit,
      formIsValid,
      errors,
      values,
      validate,
    } = useFormControls(item, validationFields)

    useEffect(() => {
      // revalidate when initing page
      let isRequired = true
      // if exist checked all day, not validate completionTimeFrom, completionTimeTo
      if (values.completionAllDay) {
        isRequired = false
      }

      validationFields.completionTimeFrom.rule.required = isRequired
      validationFields.completionTimeTo.rule.required = isRequired
      validate(validationFields)
    }, [values.completionAllDay])

    useEffect(() => {
      handleReset(cloneDeep(item))
    }, [item])

    const onHandleCloseModal = (modal: boolean): void => {
      handleCloseModal(modal)
    }

    const handleChangeTypeId = (name: string, value: string | number | number[]): void => {
      handleInputValue(name, value)
    }
    const handleChangeName = (name: string, value: string): void => {
      handleInputValue(name, value)
    }

    const handleChangeAssignTo = (name: string, value: string | number | number[]): void => {
      handleInputValue(name, value)
    }

    const handleChangePriority = (name: string, value: string | number | number[]): void => {
      handleInputValue(name, value)
    }
    const handleChangeDescription = (name: string, value: string): void => {
      handleInputValue(name, value)
    }
    const handleSetAttachedFiles = (name: string, files: AttachedFile[]): void => {
      handleInputValue(name, files)
    }

    const handleChangeRelatedType = (name: string, value: string | number): void => {
      handleInputValue(name, value)
    }

    const handleChangeRelatedTo = (name: string, value?: number | null): void => {
      handleInputValue(name, value)
    }

    const handleCompletedTimeAllDay = (value: boolean): void => {
      let isRequired = true
      // if exist checked all day, not validate completionTimeFrom, completionTimeTo
      if (values.completionAllDay) {
        isRequired = false
      }
      validationFields.completionTimeFrom.rule.required = !isRequired
      validationFields.completionTimeTo.rule.required = !isRequired

      handleInputValue('completionAllDay', value, validationFields)
    }

    const handleCompletedTimeDate = (value?: number): void => {
      handleInputValue('completionTimeDate', value)
    }

    const handleCompletedTimeFrom = (value?: number): void => {
      handleInputValue('completionTimeFrom', value)
    }

    const handleCompletedTimeTo = (value?: number | string | undefined): void => {
      handleInputValue('completionTimeTo', value)
    }

    const handleChangeRemider = (name: string, value: number): void => {
      handleInputValue(name, value)
    }

    const handleChangeLocation = (name: string, value: string): void => {
      handleInputValue(name, value)
    }

    const validateSubmitForm = (): boolean => {
      let isDisabled = true
      if (formIsValid()) {
        isDisabled = false
      }

      return isDisabled
    }

    const saveTodoTask = async (): Promise<void> => {
      // convert completed time to number
      const { completionAllDay } = values

      let { completionTimeDate, completionTimeFrom, completionTimeTo, reminderTime } = values
      if (completionAllDay) {
        completionTimeFrom = null
        completionTimeTo = null
        reminderTime = null
      } else {
        if (values.completionTimeFrom < currentTime) {
          alert(ERROR_TIME_IN_THE_PAST)

          return
        }

        if (typeof values.completionTimeDate !== 'number') {
          completionTimeDate = moment(values.completionTimeDate)
        }

        if (typeof values.completionTimeFrom !== 'number') {
          completionTimeFrom = moment(values.completionTimeFrom, TIME_FORMAT)
        }
        if (typeof values.completionTimeTo !== 'number') {
          completionTimeTo = moment(values.completionTimeTo, TIME_FORMAT)
        }
      }

      const dataPost = {
        ...item,
        ...values,
        completionTimeDate,
        completionTimeFrom,
        completionTimeTo,
        reminderTime,
        taskId: id,
      }

      handleSubmitForm(dataPost)
    }

    return (
      <Dialog
        className="dialog-container"
        fullWidth
        maxWidth="md"
        open={displayModal}
        scroll="body"
        onClose={() => onHandleCloseModal(displayModal)}
      >
        <Toolbar className="dialog-toolbar">
          <Typography className="title" variant="h6">
            {title}
          </Typography>
          <IconButton className="icon-close" onClick={() => onHandleCloseModal(displayModal)}>
            &times;
          </IconButton>
        </Toolbar>
        <DialogContent className="dialog-content" dividers>
          <Grid container spacing={2}>
            <Grid item xs={6}>
              <ISelectJobType placeholder={PLACEHOLDER_JOB_TYPE_SINGLE} errMsg={errors.type} required value={values.type} onChange={handleChangeTypeId} />
            </Grid>
            <Grid item xs={6} />
            <Grid item xs={12}>
              <TextInput
                error={errors.name}
                id="name"
                label={LABEL_JOB_NAME}
                maxLength={MAX_LENGTH_INPUT}
                name="name"
                placeholder={PLACEHOLDER_JOB_NAME}
                required
                type="text"
                value={values.name}
                onChange={handleChangeName}
              />
            </Grid>
            <Grid item xs={6}>
              <ISelectAssignee
                errMsg={errors.assignedTo}
                placeholder={PLACEHOLDER_ASSIGNEE_SINGLE}
                required
                value={values.assignedTo}
                onChange={handleChangeAssignTo}
              />
            </Grid>
            <Grid item xs={6}>
              <ISelectPriority
                errMsg={errors.priority}
                placeholder={PLACEHOLDER_PRIORITY_SINGLE}
                required
                value={values.priority}
                onChange={handleChangePriority}
              />
            </Grid>
            <Grid item xs={12}>
              <TextInput
                error={errors.description}
                id="description"
                label={LABEL_DESCRIPTION}
                multiline
                name="description"
                required
                rows={4}
                type="text"
                value={values.description}
                onChange={handleChangeDescription}
              />
            </Grid>
            <Grid item xs={12}>
              <Attachment
                fileList={values.attachedFiles}
                id="attachment"
                name="attachedFiles"
                onSetFile={handleSetAttachedFiles}
              />
            </Grid>
            <Grid item xs={12}>
              <SearchBar
                assignedTo={values.assignedTo}
                errMsg={errors.relatedTo}
                id={id}
                relatedType={values.relatedType}
                relatedTo={values.relatedTo}
                onChangeRelatedTo={handleChangeRelatedTo}
                onChangeRelatedType={handleChangeRelatedType}
              />
            </Grid>
            <Grid item xs={12}>
              <CompletedTime
                error={errors}
                id="completedTime"
                name="completedTime"
                completionAllDay={values.completionAllDay}
                completionTimeDate={values.completionTimeDate}
                completionTimeFrom={values.completionTimeFrom}
                completionTimeTo={values.completionTimeTo}
                onChangeCompletedTimeAllDay={handleCompletedTimeAllDay}
                onChangeCompletedTimeDate={handleCompletedTimeDate}
                onChangeCompletedTimeFrom={handleCompletedTimeFrom}
                onChangeCompletedTimeTo={handleCompletedTimeTo}
              />
            </Grid>
            <Grid item xs={6}>
              <ISelectRemider
                disabled={values.completionAllDay}
                value={values.reminderTime}
                onChange={handleChangeRemider}
              />
            </Grid>
            <Grid item xs={6} />
            <Grid item xs={12}>
              <SelectLocation
                errMsg={errors.location}
                value={values.location}
                onChange={handleChangeLocation}
                onInputChange={handleChangeLocation}
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions className="dialog-actions">
          <Button autoFocus variant="outlined" onClick={() => onHandleCloseModal(displayModal)}>
            {BTN_CANCEL}
          </Button>
          <Button autoFocus color="primary" disabled={validateSubmitForm()} variant="contained" onClick={saveTodoTask}>
            {BTN_SAVE}
          </Button>
        </DialogActions>
      </Dialog>
    )
  },
)
