import React, { useCallback, useEffect, useState, useMemo } from 'react'

import { ISelectStatusProps } from 'modules/ISelectStatus/index.d'
import { getStatusList } from 'services/api/GetOptions'

import { TAutocomplete } from 'components/TAutocomplete'

import { parseOptions } from 'utils/renderComponent'
import { IOption } from 'utils/form'
import { isNotEmpty } from 'utils/util'

import { LABEL_STATUS, RESPONSE_GET_SUCCESS, PLACEHOLDER_STATUS, PLACEHOLDER_STATUS_HAS_VALUE } from 'constants/common'

export const ISelectStatus = (props: ISelectStatusProps): JSX.Element => {
  const { errMsg, multiple, placeholder = PLACEHOLDER_STATUS, required, value, onChange } = props

  const itemName = 'status'

  const memoPlaceholder = useMemo(() => {
    return isNotEmpty(value) ? PLACEHOLDER_STATUS_HAS_VALUE : placeholder
  }, [placeholder, value])

  const [optionItems, setOptionItems] = useState<IOption[]>([])

  const fetchData = useCallback(async (): Promise<void> => {
    let hasData = false
    const items = await getStatusList()

    if (!hasData && items.code === RESPONSE_GET_SUCCESS) {
      // check data exist and length
      if (items.data || (items.data && items.data.length > 0)) {
        hasData = true
        // parse response items
        let parseData = parseOptions(itemName, items.data)
        parseData = optionItems.concat(parseData)

        // update options
        setOptionItems(parseData)
      }
    }
  }, [optionItems])

  useEffect(() => {
    fetchData()
  }, [])

  const handleChange = (name: string, val: null | number | number[]): void => {
    if (multiple) {
      onChange(name, val as number[])
    } else {
      onChange(name, val ? [val as number] : [])
    }
  }

  return (
    <TAutocomplete
      error={errMsg}
      id={itemName}
      label={LABEL_STATUS}
      multiple={multiple}
      name={itemName}
      option={optionItems}
      placeholder={memoPlaceholder}
      required={required}
      value={value}
      onChange={handleChange}
    />
  )
}
