/* eslint-disable no-unused-vars */
/* eslint-disable react/jsx-props-no-spreading */
import React from 'react'
import { TextField, FormHelperText, Typography } from '@material-ui/core'
import Autocomplete from '@material-ui/lab/Autocomplete'

import { TAutocompleteProps } from 'components/TAutocomplete/index.d'

import 'components/ISelect/index.scss'
import isArray from 'lodash/isArray'
import { IOption } from 'utils/form'

export const TAutocomplete: React.FC<TAutocompleteProps> = (props) => {
  const {
    error,
    label,
    required,
    className,
    disabled,
    id,
    option,
    placeholder,
    value = null,
    name,
    onChange,
    multiple,
  } = props

  let fValue = value

  if (multiple) {
    if (!isArray(value)) {
      fValue = []
    }
  } else {
    // eslint-disable-next-line no-lonely-if
    if (isArray(value)) {
      ;[fValue] = value
    }
  }

  let selectedOptions: IOption[] | IOption | null

  if (multiple) {
    const values = fValue as number[]
    selectedOptions = option && option.length > 0 && option.filter((el) => values.includes(+el.value)) || []
  } else {
    selectedOptions = option.find((el) => el.value === fValue) || null
  }

  const handleChange = (event: any, eValue: any): void => {
    let payload

    if (multiple) {
      payload = (eValue as IOption[]).map((el) => el.value)
    } else {
      payload = eValue?.value || ''
    }
    onChange(name, payload)
  }

  return (
    <>
      {label && (
        <Typography className={`${required ? 'required' : ''}`} component="p" variant="subtitle2">
          {label}
        </Typography>
      )}

      <Autocomplete
        limitTags={3}
        size="small"
        className={className}
        multiple={multiple}
        id={id}
        value={selectedOptions}
        disabled={disabled}
        onChange={handleChange}
        getOptionLabel={(options: IOption) => {
          return options.label || ''
        }}
        renderOption={(otp) => (
          <>
            {otp.icon && <span style={{ marginRight: '8px' }}>{otp.icon}</span>}
            <span>{otp.label}</span>
          </>
        )}
        options={option}
        renderInput={(params) => <TextField {...params} placeholder={placeholder} variant="outlined" />}
        filterSelectedOptions
      />

      <FormHelperText id="component-error-text">{error}</FormHelperText>
    </>
  )
}
