import React from 'react'
import { FormControl, Typography } from '@material-ui/core'

import { ISelect } from 'components/ISelect'
import { ISelectOptionProps } from 'components/ISelectOption/index.d'

export const ISelectOption: React.FC<ISelectOptionProps> = (props) => {
  const { className, disabled, id, label, name, option, placeholder, required, value, onChange } = props

  let clsName = ''
  if (required) {
    clsName = 'required'
  }

  return (
    <>
      <Typography className={clsName} component="p" variant="subtitle2">
        {label}
      </Typography>
      <FormControl fullWidth>
        <ISelect
          className={className}
          disabled={disabled}
          id={id}
          name={name}
          option={option}
          placeholder={placeholder}
          value={value}
          onChange={onChange}
        />
      </FormControl>
    </>
  )
}
