import React, { memo, useState } from 'react'

import DateFnsUtils from '@date-io/date-fns'
import 'date-fns'

import AccessTimeIcon from '@material-ui/icons/AccessTime'
import { KeyboardDatePicker, MuiPickersUtilsProvider, KeyboardTimePicker } from '@material-ui/pickers'
import { FormControl, FormControlLabel, Checkbox, Grid, Typography, FormHelperText } from '@material-ui/core'

import moment from 'moment'

import { TCompletedTime } from 'modules/CompletedTime/index.d'

import { currentTime } from 'constants/defaultValue'
import { DATE_FORMAT, DATE_FORMAT_MAIN, ERROR_TIMETO_LESS_TIMEFROM, ERROR_TIME_IN_THE_PAST, LABEL_COMPLETED_TIME, TIME_FORMAT } from 'constants/common'

import { getStartDate } from 'utils/util'

import 'modules/CompletedTime/styles.scss'

export const CompletedTime: React.FC<TCompletedTime> = memo((props) => {
  const {
    error,
    id,
    name,
    completionAllDay,
    completionTimeDate,
    completionTimeFrom,
    completionTimeTo,
    onChangeCompletedTimeAllDay,
    onChangeCompletedTimeDate,
    onChangeCompletedTimeFrom,
    onChangeCompletedTimeTo,
  } = props

  let checkTimeDate = null
  if (completionTimeDate) {
    checkTimeDate = completionTimeDate
  }
  let checkTimeFrom = null
  if (completionTimeFrom) {
    checkTimeFrom = completionTimeFrom
  }
  let checkTimeTo = null
  if (completionTimeTo) {
    checkTimeTo = completionTimeTo
  }

  const [validateTimeFrom, setValidateTimeFrom] = useState(false)
  const [validateTimeTo, setValidateTimeTo] = useState(false)

  let hasError = false
  let clsDateTime = 'datepicker dateTime'
  if (error && error.completionTimeDate) {
    clsDateTime += ' error'
    hasError = true
  }
  let clsStartTime = 'datepicker startTime'
  if (error && error.completionTimeFrom) {
    clsStartTime += ' error'
    hasError = true
  }
  let clsFinishTime = 'datepicker finishTime'
  if (error && error.completionTimeTo) {
    clsFinishTime += ' error'
    hasError = true
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const handleCheckAllDay = (checked: boolean): void => {
    onChangeCompletedTimeAllDay(checked)
  }
  const handleDateChange = (date: any): void => {
    const getDate = getStartDate(date, 'day')

    onChangeCompletedTimeDate(getDate)
  }

  const handleStartTimeChange = (date?: number | string): void => {
    let timeFrom: any
    if (date) {
      timeFrom = moment(date).format(TIME_FORMAT)
      if (completionTimeDate) {
        const getDate = moment(completionTimeDate).format(DATE_FORMAT_MAIN)
        timeFrom = moment(`${getDate} ${timeFrom}`, `${DATE_FORMAT_MAIN} ${TIME_FORMAT}`).unix() * 1000
      }
    }

    const minTime = currentTime
    let errorFrom = true
    if (minTime < timeFrom) {
      // not error, set time from value 
      errorFrom = false

      if (completionTimeTo && completionTimeTo < timeFrom) {
        errorFrom = true
      }
    }
    // display error
    setValidateTimeFrom(errorFrom)
    onChangeCompletedTimeFrom(timeFrom)
  }

  const handleEndTimeChange = (date?: number | string): void => {
    let timeTo
    if (date) {
      timeTo = moment(date).format(TIME_FORMAT)
      if (completionTimeDate) {
        const getDate = moment(completionTimeDate).format(DATE_FORMAT_MAIN)
        timeTo = moment(`${getDate} ${timeTo}`, `${DATE_FORMAT_MAIN} ${TIME_FORMAT}`).unix() * 1000
      }
    }

    const minTime = completionTimeFrom
    let errorTo = true
    if (moment(minTime).isBefore(timeTo)) {
      // not error, set time to value
      errorTo = false
    }

    // display error
    setValidateTimeTo(errorTo)
    onChangeCompletedTimeTo(timeTo)
  }

  return (
    <div>
      <Typography className="required" variant="subtitle2" component="p">
        {LABEL_COMPLETED_TIME}
      </Typography>
      <Grid container spacing={2} alignItems="center">
        <Grid item xs={3}>
          <FormControl>
            <FormControlLabel
              value="end"
              control={
                <Checkbox
                  checked={completionAllDay}
                  color="primary"
                  id={`${id}-checkbox`}
                  name={`${name}-checkbox`}
                  onChange={(e, checked) => handleCheckAllDay(checked)}
                />
              }
              label="C ngy"
              labelPlacement="end"
            />
          </FormControl>
        </Grid>
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
          <Grid item xs={3}>
            <KeyboardDatePicker
              className={clsDateTime}
              disableToolbar
              format={DATE_FORMAT}
              helperText=""
              id={`${id}-date`}
              inputVariant="outlined"
              margin="dense"
              minDate={currentTime}
              name={`${name}-date`}
              placeholder={DATE_FORMAT.toLowerCase()}
              variant="inline"
              value={checkTimeDate}
              onChange={handleDateChange}
            />
          </Grid>
          <Grid item xs={3}>
            <KeyboardTimePicker
              ampm={false}
              className={clsStartTime}
              keyboardIcon={<AccessTimeIcon />}
              helperText=""
              inputVariant="outlined"
              id={`${id}-startTime`}
              disabled={completionAllDay}
              margin="dense"
              name={`${name}-startTime`}
              placeholder={TIME_FORMAT.toLowerCase()}
              value={checkTimeFrom}
              onChange={(date) => handleStartTimeChange(moment(date).unix() * 1000)}
            />
          </Grid>
          <Grid item xs={3}>
            <KeyboardTimePicker
              ampm={false}
              className={clsFinishTime}
              disabled={completionAllDay}
              helperText=""
              inputVariant="outlined"
              id={`${id}-finishTime`}
              keyboardIcon={<AccessTimeIcon />}
              margin="dense"
              name={`${name}-finishTime`}
              placeholder={TIME_FORMAT.toLowerCase()}
              value={checkTimeTo}
              onChange={(date) => handleEndTimeChange(moment(date).unix() * 1000)}
            />
          </Grid>
        </MuiPickersUtilsProvider>

        {hasError && (
          <FormHelperText id="component-error-text" className="error-full-width">
            Vui lng nhp {LABEL_COMPLETED_TIME}
          </FormHelperText>
        )}
        {validateTimeFrom && (
          <FormHelperText id="component-error-text" className="error-full-width">
            {ERROR_TIME_IN_THE_PAST}
          </FormHelperText>
        )}
        {validateTimeTo && (
          <FormHelperText id="component-error-text" className="error-full-width">
            {ERROR_TIMETO_LESS_TIMEFROM}
          </FormHelperText>
        )}
      </Grid>
    </div>
  )
})
