import React, { useCallback, useContext, useEffect, useState } from 'react'

import { FormHelperText, TextField, Typography } from '@material-ui/core'

import { TSelectLocation } from 'modules/SelectLocation/index.d'

import { ISelectSuggestionFreeText } from 'components/ISelectSuggestionFreeText'

import { getLocationTcList } from 'services/api/GetOptions'

import { LABEL_LOCATION, RESPONSE_GET_SUCCESS } from 'constants/common'
import { CommonContext } from 'context/CommonContext'

import { parseLocationTcOptions } from 'utils/renderComponent'
import { IOption } from 'utils/form'

import 'modules/SelectLocation/styles.scss'

export const SelectLocation: React.FC<TSelectLocation> = (props) => {
  const { setLoading } = useContext(CommonContext)
  const { errMsg, required, value, onChange, onInputChange } = props

  let clsName = ''
  if (required) {
    clsName = 'required'
  }

  const itemName = 'location'
  const [optionItems, setOptionItems] = useState<IOption[]>([])

  const fetchData = useCallback(async (): Promise<[]> => {
    setLoading(true)

    const resp = await getLocationTcList()

    setLoading(false)
    if (resp.code === RESPONSE_GET_SUCCESS) {
      // update options
      // check data exist and length
      if (resp.data.list.length > 0) {
        // parse response items
        const parseData = parseLocationTcOptions(itemName, resp.data.list)

        // update options
        setOptionItems(parseData)
      }
    }

    return []
  }, [optionItems])
  useEffect(() => {
    fetchData()
  }, [])

  return (
    <div className="select-location">
      <Typography className={clsName} variant="subtitle2" component="p">
        {LABEL_LOCATION}
      </Typography>
      <ISelectSuggestionFreeText
        id="location"
        name="location"
        options={optionItems}
        value={value}
        onChange={onChange}
        onInputChange={onInputChange}
        renderInput={(params) => (
          <TextField
            // eslint-disable-next-line react/jsx-props-no-spreading
            {...params}
            className="input-content"
            name="location"
            margin="dense"
            variant="outlined"
          />
        )}
      />
      <FormHelperText id="component-error-text">{errMsg}</FormHelperText>
    </div>
  )
}
