/* eslint-disable react/jsx-props-no-spreading */
import React, { ChangeEvent } from 'react'
import { createStyles, Theme, withStyles } from '@material-ui/core/styles'
import InputBase from '@material-ui/core/InputBase'
import FormControl from '@material-ui/core/FormControl'
import { TTextInput } from 'components/TextInput/index.d'
import { FormHelperText, Typography } from '@material-ui/core'

const BootstrapInput = withStyles((theme: Theme) =>
  createStyles({
    root: {
      'label + &': {
        marginTop: theme.spacing(3),
      },
    },
    input: {
      borderRadius: 4,
      backgroundColor: theme.palette.common.white,
      border: '1px solid #ced4da',
      padding: '10px 12px',
      transition: theme.transitions.create(['border-color', 'box-shadow']),
      '&:focus': {},
    },
  }),
)(InputBase)

export const TextInput: React.FC<TTextInput> = (props) => {
  const { error, id, maxLength, label, multiline, name, placeholder, required, rows, rowsMax, value, onChange } = props

  const getValue = value || ''

  const handleChange = (e: ChangeEvent<HTMLInputElement>): void => {
    onChange(e.target.name, e.target.value)
  }

  return (
    <div>
      <Typography className={`${required ? 'required' : ''}`} variant="subtitle2" component="p">
        {label}
      </Typography>
      <FormControl fullWidth>
        <BootstrapInput
          fullWidth
          id={id}
          inputProps={{
            maxLength
          }}
          multiline={multiline}
          placeholder={placeholder}
          rows={rows}
          rowsMax={rowsMax}
          name={name}
          value={getValue}
          onChange={handleChange}
        />

        <FormHelperText id="component-error-text">{error}</FormHelperText>
      </FormControl>
    </div>
  )
}
