import React from 'react'

import { Paper, Table, TableBody, TableCell, TableContainer, TableRow, Typography } from '@material-ui/core'

import moment from 'moment'

import { TLabelValue } from 'components/TLabelValue'
import { TTitle } from 'components/TTitle'

import { DATE_FORMAT_MAIN, LABEL_ALL_DAY, NO_OPTIONS, TIME_FORMAT } from 'constants/common'
import { currentTime, OPTIONS_GROUP_BY } from 'constants/defaultValue'

import { renderPriority, renderTrangThai } from 'utils/renderComponent'
import { Event } from 'react-big-calendar'

export const AgendaView = ({ events }: any): any => {
  const handleClickTaskName = (id: number): void => {
    localStorage.setItem(
      'view_detail_task_tracking',
      JSON.stringify({
        source: 'calendar',
        mode: 'agenda',
      }),
    )

    window.open(`/calendar-task/detail/${id}`, '_blank')
  }

  const renderGroupAgent = (resource: any, idx?: string): JSX.Element => {
    resource.list.sort((a: any, b: any) => b.priority - a.priority)
    resource.list.sort((a: any, b: any) => a.status - b.status)
    resource.list.sort((a: any, b: any) => b.completionAllDay - a.completionAllDay)
    resource.list.sort((a: any, b: any) => a.completionTimeFrom - b.completionTimeFrom)
    resource.list.sort((a: any, b: any) => a.completionTimeDate - b.completionTimeDate)

    return resource.list && resource.list.map((item: any, k: number) => {
      const countItems = resource.list.length

      const key = `todo-agent-${idx}-${k}-${item.assignedTo}`
      let classIsThePast = ''
      // if selected allday
      if (item.completionTimeFrom && item.completionTimeFrom < currentTime) {
        classIsThePast = 'is-the-past'
      }

      let renderTime = LABEL_ALL_DAY
      if (!item.completionAllDay) {
        if (item.completionTimeFrom < currentTime) {
          classIsThePast = 'is-the-past'
        }

        const completionTimeFrom = moment(item.completionTimeFrom).format(TIME_FORMAT)
        const completionTimeTo = moment(item.completionTimeTo).format(TIME_FORMAT)
        renderTime = `${completionTimeFrom} - ${completionTimeTo}`
      }

      let renderAgent = null
      if (k === 0) {
        renderAgent = <TableCell className="v-align-top" rowSpan={countItems} component="td">
          {item.assignedToName} ({countItems})
        </TableCell>
      }

      return (
        <TableRow className="todo-row" key={key}>
          {renderAgent}
          <TableCell align="left">
            <Typography className={classIsThePast} component="div">
              {moment(item.completionTimeDate).format(DATE_FORMAT_MAIN)}
            </Typography>
          </TableCell>
          <TableCell align="left" className="row-time">
            <Typography className={classIsThePast} component="div">
              {renderTime}
            </Typography>
          </TableCell>
          <TableCell align="left" className="row-name">
            <Typography
              onClick={() => handleClickTaskName(item.id)}
              component="a"
            >
              {item.name}
            </Typography>
          </TableCell>
          <TableCell align="right">
            <TLabelValue className="no-padding" value={renderPriority(item.priority)} />
          </TableCell>
          <TableCell align="left">
            <TLabelValue className="no-padding" value={renderTrangThai(item.status)} />
          </TableCell>
          <TableCell align="left">
            <TLabelValue
              className="no-padding"
              value={<TTitle className="item-value" title={<span>{item.typeName}</span>} />}
            />
          </TableCell>
        </TableRow>
      )
    })
  }
  const renderGroupDate = (resource: any, idx?: string): JSX.Element => {
    resource.list.sort((a: any, b: any) => b.priority - a.priority)
    resource.list.sort((a: any, b: any) => a.status - b.status)
    resource.list.sort((a: any, b: any) => b.completionAllDay - a.completionAllDay)
    resource.list.sort((a: any, b: any) => a.completionTimeFrom - b.completionTimeFrom)

    return resource.list && resource.list.map((item: any, k: number) => {
      let renderTime = LABEL_ALL_DAY
      if (!item.completionAllDay) {
        const completionTimeFrom = moment(item.completionTimeFrom).format(TIME_FORMAT)
        const completionTimeTo = moment(item.completionTimeTo).format(TIME_FORMAT)
        renderTime = `${completionTimeFrom} - ${completionTimeTo}`
      }

      const key = `todo-date-${k}-${idx}-${item.completionTimeDate}`
      let classIsThePast = 'row-time'
      // if selected allday
      if (item.completionTimeFrom && item.completionTimeFrom < currentTime) {
        classIsThePast += ' is-the-past'
      }

      if (!item.completionAllDay) {
        if (item.completionTimeFrom < currentTime) {
          classIsThePast += ' is-the-past'
        }
      }

      let renderDate = null
      if (k === 0) {
        renderDate = <TableCell className={`${classIsThePast} v-align-top`} rowSpan={resource.list.length}>
          {moment(item.completionTimeDate).format(DATE_FORMAT_MAIN)}
        </TableCell>
      }

      return (
        <TableRow className="todo-row" key={key}>
          {renderDate}
          <TableCell align="left" className={classIsThePast}>
            {renderTime}
          </TableCell>
          <TableCell align="left" className="row-name">
            {item.name}
          </TableCell>
          <TableCell align="right">
            <TLabelValue className="no-padding" value={renderPriority(item.priority)} />
          </TableCell>
          <TableCell align="left">
            <TLabelValue className="no-padding" value={renderTrangThai(item.status)} />
          </TableCell>
          <TableCell align="left">
            <TLabelValue
              className="no-padding"
              value={<TTitle className="item-value" title={<span>{item.typeName}</span>} />}
            />
          </TableCell>
          <TableCell component="th" scope="row">
            {item.assignedToName}
          </TableCell>
        </TableRow>
      )
    })
  }

  const renderEvent = (ev: Event, k: string): JSX.Element => {
    const { resource } = ev

    if (resource.groupBy === OPTIONS_GROUP_BY[0].value) {
      return renderGroupAgent(resource, k)
    }

    return renderGroupDate(resource, k)
  }

  const renderBody = (data: any): JSX.Element | '' => {
    if (!data || data.length === 0) {
      return (
        <TableRow className="todo-row">
          <TableCell component="th" scope="row">
            {NO_OPTIONS}
          </TableCell>
        </TableRow>
      )
    }

    return data.map((ev: Event, k: number) => {
      const key = `data-${k}`
      if (ev.resource.list) {
        return renderEvent(ev, key)
      }

      return (
        <TableRow className="todo-row" key={key}>
          <TableCell component="th" scope="row">&nbsp;</TableCell>
        </TableRow>
      )
    })
  }

  return (
    <TableContainer className="agenda-container" component={Paper}>
      <Table className="calendar-list" aria-label="simple table">
        <TableBody>{renderBody(events)}</TableBody>
      </Table>
    </TableContainer>
  )
}

AgendaView.title = (start: Date, { localizer }: any): any => {
  const end = moment(start).add(1, 'month')
  return localizer.format({ start, end }, 'agendaHeaderFormat')
}

AgendaView.navigate = (date: Date, action: 'PREV' | 'NEXT'): any => {
  const sDate = moment(date).startOf('month').toDate()
  switch (action) {
    case 'PREV':
      return moment(sDate).subtract(1, 'month').toDate()
    case 'NEXT':
      return moment(sDate).add(1, 'month').toDate()
    default:
      return date
  }
}
