import React from 'react'

import moment from 'moment'

import { IViewCreatedDateProps } from 'modules/IViewCreatedDate/index.d'

import { TLabelValue } from 'components/TLabelValue'
import { TTitle } from 'components/TTitle'

import { MOMENT_DATE_FORMAT, TIME_FORMAT } from 'constants/common'

export const IViewCreatedDate = (props: IViewCreatedDateProps): JSX.Element => {
  const { label, value } = props

  return (
    <TLabelValue
      className="no-padding"
      label={label}
      value={
        <TTitle
          className="item-value"
          title={<span>{moment(value).format(`${MOMENT_DATE_FORMAT} ${TIME_FORMAT}`)}</span>}
        />
      }
    />
  )
}
