import React, { useContext } from 'react'
import { v4 as uuidv4 } from 'uuid'

import { TodoForm } from 'pages/TodoList/TodoForm'

import { ICreateTask } from 'modules/CreateTask/index.d'

import { IToDoTaskDataPost } from 'utils/form'

import { CommonContext } from 'context/CommonContext'

import { submitCreateTask } from 'services/api/Task'
import taskTrackingService from 'services/taskTrackingService'

import { RESPONSE_GET_SUCCESS } from 'constants/common'

import 'modules/CreateTask/styles.scss'

const CreateTask = (props: ICreateTask): JSX.Element => {
  const { setLoading } = useContext(CommonContext)

  const { dataPost, displayModal, title, handleListValue, setDisplayModal } = props

  const handleCloseModal = (modal: boolean, isAddOrUpdate?: boolean): void => {
    setDisplayModal(modal, isAddOrUpdate)
  }

  const handleSubmitForm = async (data: IToDoTaskDataPost): Promise<void> => {
    // set Loading
    setLoading(true)

    const resp = await submitCreateTask(data)

    setLoading(false)

    alert(resp.message)
    if (resp.code === RESPONSE_GET_SUCCESS) {
      // close modal
      handleCloseModal(displayModal, true)

      taskTrackingService.sendEvent({
        eventName: 'cal_user_create_task',
        timestamp: +new Date(),
        eventTransactionId: uuidv4(),
      })
    }
  }

  return (
    <TodoForm
      displayModal={displayModal}
      item={dataPost}
      title={title}
      handleCloseModal={(status: boolean) => handleCloseModal(status)}
      handleSubmitForm={handleSubmitForm}
      setDisplayModal={setDisplayModal}
      setTodoTask={handleListValue}
    />
  )
}

export default CreateTask
