import React, { useContext, useEffect, useState } from 'react'

import { ImageList, ImageListItem, Typography } from '@material-ui/core'
import CheckCircleIcon from '@material-ui/icons/CheckCircle'
import Lightbox from 'react-image-lightbox'

import { TCommentCategoryProps } from 'modules/TCommentCategoryContent/index.d'

import 'modules/TCommentCategoryContent/index.scss'
import { CommentData, CommentDataItem } from 'model/comment'
import {
  TaskStatusType,
  RESPONSE_GET_SUCCESS,
  TITLE_COMMENT_COMPLETED_RESULT,
  TITLE_COMMENT_CANCELED_RESULT,
} from 'constants/common'
import { getCommentByTaskId } from 'services/api/Task'
import { CommonContext } from 'context/CommonContext'

export const TCommentCategoryContent = (props: TCommentCategoryProps): JSX.Element => {
  const { status, id } = props

  const { setLoading } = useContext(CommonContext)
  const [comments, setComments] = useState<CommentData[]>([])
  const [open, setOpen] = useState(false)
  const [photoIndex, setPhotoIndex] = useState(0)
  const [photoList, setPhotoList] = useState<string[]>([])
  const [commentTitle, setCommentTitle] = useState<string>('')
  const [clsTitle, setClsTitle] = useState<string>('')

  useEffect(() => {
    if (status === TaskStatusType.COMPLETED) {
      setClsTitle('primary')
      setCommentTitle(TITLE_COMMENT_COMPLETED_RESULT)
    }

    if (status === TaskStatusType.CANCELED) {
      setCommentTitle(TITLE_COMMENT_CANCELED_RESULT)
      setClsTitle('secondary')
    }
  }, [status])

  useEffect(() => {
    const fetchComments = async (): Promise<void> => {
      setLoading(true)
      const response = await getCommentByTaskId(id)
      setLoading(false)
      if (response.code === RESPONSE_GET_SUCCESS) {
        if (response.data) {
          setComments(response.data)
        }
      }
    }
    fetchComments()
  }, [])

  const getCommentItemString = (items: CommentDataItem[]): string => {
    return items.map((i: CommentDataItem) => i.comment).join(', ')
  }

  const renderLightbox = (): JSX.Element | false => {
    return (
      open && (
        <Lightbox
          mainSrc={photoList[photoIndex]}
          nextSrc={photoList[(photoIndex + 1) % photoList.length]}
          prevSrc={photoList[(photoIndex + photoList.length - 1) % photoList.length]}
          onCloseRequest={() => setOpen(false)}
          onMovePrevRequest={() => setPhotoIndex((photoIndex + photoList.length - 1) % photoList.length)}
          onMoveNextRequest={() => setPhotoIndex((photoIndex + 1) % photoList.length)}
        />
      )
    )
  }

  const previewPhoto = (index: number, pList: string[]): void => {
    setPhotoIndex(index)
    setOpen(true)
    setPhotoList(pList)
  }

  const renderPhotos = (photoString: string): JSX.Element => {
    const photoLinks = JSON.parse(photoString)
    if (Array.isArray(photoLinks) && photoLinks.length > 0) {
      return (
        <ImageList className="image-list" cols={12}>
          {photoLinks.map((p: any, i: number) => (
            <ImageListItem
              key={p.link}
              onClick={() => {
                previewPhoto(
                  i,
                  // eslint-disable-next-line @typescript-eslint/no-explicit-any
                  photoLinks.map((pLink: any) => pLink.link),
                )
              }}
              aria-hidden="true"
            >
              <img key={p.link} src={p.link} alt="" />
            </ImageListItem>
          ))}
        </ImageList>
      )
    }
    return <></>
  }

  const renderLayout = (data: CommentData[]): JSX.Element | false => {
    return (
      data.length > 0 && (
        <div className={`mt-40 comment-data-container status-${status}`}>
          <div className="comment-category-content">
            <CheckCircleIcon className={`icon ${clsTitle}`} />
            <div className="comment-items">
              <Typography className={`title ${clsTitle}`} component="div">
                {commentTitle}
              </Typography>
              {data.map((c) => (
                <div key={`comment-item-${c.id}`} className="comment-item">
                  <div className="files-container">{renderPhotos(c.photo)}</div>
                  <Typography className="comment-note" component="div">
                    <Typography className="comment-note-label" component="div">
                      {c.categoryItemName}
                    </Typography>
                    <Typography className="comment-note-value" component="div">
                      {getCommentItemString(c.items)}
                    </Typography>
                  </Typography>
                </div>
              ))}
            </div>
          </div>
        </div>
      )
    )
  }

  return (
    <div>
      {renderLayout(comments)}
      {renderLightbox()}
    </div>
  )
}
