import React from 'react'
import { MenuItem, Select, Typography } from '@material-ui/core'

import { ISelectProps } from 'components/ISelect/index.d'

import 'components/ISelect/index.scss'

export const ISelect: React.FC<ISelectProps> = (props) => {
  const { className, disabled, id, name, native, option, value, onChange } = props

  let revertValue = value
  if (!value) {
    revertValue = ''
  }

  const renderOptions = (): JSX.Element[] => {
    return option.map((item) => {
      return (
        <MenuItem className="style-menu-item" key={item.key} value={item.value}>
          {item.icon}
          <Typography variant="inherit" component="span">
            {item.label}
          </Typography>
        </MenuItem>
      )
    })
  }

  const handleChange = (e: any): void => {
    if (e.target.name) {
      onChange(e.target.name, e.target.value)
    }
  }

  return (
    <Select
      className={`style-select-option ${className}`}
      defaultValue=""
      displayEmpty
      disabled={disabled}
      id={id}
      name={name}
      margin="dense"
      native={native}
      variant="outlined"
      value={revertValue}
      onChange={handleChange}
    >
      {renderOptions()}
    </Select>
  )
}
