import { Stomp, CompatClient } from '@stomp/stompjs'
import { v4 as uuidv4 } from 'uuid'
import SockJS from 'sockjs-client'
import { getUserInfo } from 'utils/util'

const URL = process.env.REACT_APP_TASK_TRACKING_URL || ''
const EVENT_SOURCE_CODE = process.env.REACT_APP_TASK_TRACKING_EVENT_SOURCE_CODE || ''
const EVENT_APP_CODE = process.env.REACT_APP_TASK_TRACKING_EVENT_APP_CODE || ''
const DESTINATION = process.env.REACT_APP_TASK_TRACKING_DESTINATION || ''

class TaskTrackingService {
  public client: CompatClient | null = null

  public isConnected = false

  public isConnecting = false

  public async connect(): Promise<void> {
    return new Promise((rs, rj) => {
      this.isConnecting = true

      this.client = Stomp.over(function() {
        return new SockJS(URL)
      })

      this.client.debug = () => null

      this.client.connect(
        {},
        () => {
          this.isConnecting = false
          this.isConnected = true
          rs()
        },
        () => {
          this.isConnecting = false
          rj()
        },
        () => {
          this.isConnected = false
        },
      )
    })
  }

  public disconnect(): void {
    if (this.client) {
      this.client.disconnect(() => {
        this.isConnected = false
      })
    }
  }

  public waitForOpenConnection = (): Promise<void> => {
    return new Promise((resolve, reject) => {
      const maxNumberOfAttempts = 40
      const intervalTime = 300
      let currentAttempt = 0

      if (this.isConnected) {
        resolve()
        return
      }

      const interval = setInterval(() => {
        if (currentAttempt > maxNumberOfAttempts - 1) {
          clearInterval(interval)
          reject(new Error('Maximum number of attempts exceeded'))
        } else if (this.isConnected) {
          clearInterval(interval)
          resolve()
        }

        currentAttempt += 1
      }, intervalTime)
    })
  }

  public async sendEvent({
    eventName,
    timestamp,
    eventTransactionId,
    eventData
  }: {
    eventName: string
    timestamp: number
    eventTransactionId: string
    eventData?: {
      [x: string]: any
    }
  }): Promise<void> {
    try {
      if (!this.isConnected && !this.isConnecting) {
        this.connect()
      }

      await this.waitForOpenConnection()

      const { userSessionId, token } = getUserInfo()

      this.client?.send(
        DESTINATION,
        {
          accessToken: token,
          userSessionId,
          requestId: uuidv4()
        },
        JSON.stringify({
          eventTypeCode: eventName,
          eventSourceCode: EVENT_SOURCE_CODE,
          eventAppCode: EVENT_APP_CODE,
          timestamp,
          eventTransactionId,
          uuid: uuidv4(),
          eventData: {
            userSessionId,
            ...eventData
          },
        }),
      )
    // eslint-disable-next-line no-empty
    } catch (error) {
    }
  }
}

export default new TaskTrackingService()
