import React, { useContext, useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'

import { Grid, Paper } from '@material-ui/core'

import { TAttachedFile } from 'pages/TodoDetail/index.d'

import { TCommentCategoryContent } from 'modules/TCommentCategoryContent'
import { IViewJobType } from 'modules/IViewJobType'
import { IViewStatus } from 'modules/IViewStatus'
import { IViewPriority } from 'modules/IViewPriority'
import { IViewCompletedTime } from 'modules/IViewCompletedTime'
import { IViewReminder } from 'modules/IViewReminder'
import { IViewDescription } from 'modules/IViewDescription'
import { IViewRelatedTo } from 'modules/IViewRelatedTo'
import { IViewLocation } from 'modules/IViewLocation'
import { IViewCreatedDate } from 'modules/IViewCreatedDate'
import { IViewAssigner } from 'modules/IViewAssigner'
import { IViewAssignedTo } from 'modules/IViewAssignedTo'

import { TButton } from 'components/TButton/index'
import { TLabelValue } from 'components/TLabelValue'
import { TTitle } from 'components/TTitle/index'

import {
  BTN_CANCEL_TODO,
  BTN_UPDATE_TODO,
  FeatureCodeType,
  IMAGE_TYPE,
  LABEL_ASSIGNED_DATE,
  LABEL_ASSIGNED_UPDATED_DATE,
  LABEL_ASSIGNEE,
  LABEL_ASSIGNER,
  PAGE_DETAIL,
  RESPONSE_GET_SUCCESS,
  TITLE_COMMENT_COMPLETED,
  TITLE_COMMENT_CANCELED,
  BTN_COMPLETE_TODO,
  TaskStatusType,
  EDIT_CREATE_TASK,
  ATTACHED_FILES,
} from 'constants/common'
import { DEFAULT_ATTACHED_FILES, TASK_DETAIL_DEFAULT_VALUE } from 'constants/defaultValue'

import { CommonContext } from 'context/CommonContext'

import { getDetailTask } from 'services/api/Task'
import { renderAttachedFiles, renderLightbox } from 'utils/renderComponent'

import 'pages/TodoDetail/index.scss'
import CommentTask from 'modules/CommentTask'
import { ITask } from 'model/task'
import EditTask from 'modules/EditTask'
import { TPERMISSION } from 'services/api/api'

import useViewDetailTaskTracking from 'utils/useViewDetailTaskTracking'

export const TodoDetail = (): JSX.Element => {
  const { checkPermission, setLoading } = useContext(CommonContext)

  const [open, setOpen] = useState<boolean>(false)
  const [photoIndex, setPhotoIndex] = useState<number>(0)
  const [modalTaskCompleted, setModalTaskCompleted] = useState<boolean>(false)
  const [modalTaskCanceled, setModalTaskCanceled] = useState<boolean>(false)
  const [title, setTitle] = useState<string>('')
  const [displayEditModal, setDisplayEditModal] = useState<boolean>(false)
  const [item, setItem] = useState<ITask>(TASK_DETAIL_DEFAULT_VALUE)
  const [isAddOrUpdate, setIsAddOrUpdate] = useState<boolean>(false)

  const [listFiles, setListFiles] = useState<TAttachedFile>(DEFAULT_ATTACHED_FILES)
  useEffect(() => {
    const filterRemainFiles = item.attachedFiles.filter((f) => ATTACHED_FILES.indexOf(f.type.toLowerCase()) > -1)
    if (filterRemainFiles.length > 0) {
      listFiles.mFile = [...listFiles.mFile, ...filterRemainFiles]
    }
    const filterImages = item.attachedFiles.filter((f) => IMAGE_TYPE.indexOf(f.type.toLowerCase()) > -1)
    if (filterImages.length > 0) {
      listFiles.image = [...listFiles.image, ...filterImages]
    }

    setListFiles(listFiles)
  }, [item])

  const { id } = useParams<{ id: string }>()

  useViewDetailTaskTracking({
    id,
    ...item
  })

  const fetchData = async (): Promise<void | JSX.Element | TPERMISSION> => {
    setLoading(true)
    const getPermission = await checkPermission('read')
    if (getPermission.code !== RESPONSE_GET_SUCCESS) {
      alert(getPermission.message)
      window.location.href = '/401-not-permission'

      return
    }

    const response = await getDetailTask(Number(id))

    setLoading(false)
    if (response.code === RESPONSE_GET_SUCCESS) {
      // check data exist and length
      if (response.data) {
        // update options
        setItem(response.data)
      }
    }
  }
  useEffect(() => {
    fetchData()
  }, [])

  useEffect(() => {
    // reload list to get updated data
    if (isAddOrUpdate) {
      fetchData()
    }
  }, [isAddOrUpdate])

  const handleCompletedTask = async (): Promise<void | JSX.Element> => {
    const getPermission = await checkPermission('update')
    if (getPermission.code !== RESPONSE_GET_SUCCESS) {
      alert(getPermission.message)

      return
    }

    setModalTaskCompleted(true)
  }

  const handleCancelTask = async (): Promise<void | JSX.Element> => {
    const getPermission = await checkPermission('update')
    if (getPermission.code !== RESPONSE_GET_SUCCESS) {
      alert(getPermission.message)

      return
    }

    setModalTaskCanceled(true)
  }

  const toggleLightBox = (i: number): number => {
    setOpen(!open)
    setPhotoIndex(i)

    return i
  }

  const toggleEditModal = async (status: boolean, taskTitle: string, isUpdate?: boolean): Promise<void> => {
    if (status) {
      const getPermission = await checkPermission('update')
      if (getPermission.code !== RESPONSE_GET_SUCCESS) {
        alert(getPermission.message)

        return
      }
    }

    setDisplayEditModal(status)
    setTitle(taskTitle)

    if (isUpdate) {
      setIsAddOrUpdate(isUpdate)
    }
  }
  const handleUpdateTask = (): void => {
    toggleEditModal(!displayEditModal, EDIT_CREATE_TASK)
  }
  const renderActionButtons = (status?: number): JSX.Element | false => {
    return (
      status === TaskStatusType.INCOMPLETE && (
        <Grid className="action-buttons" container direction="row-reverse">
          <TButton className="btn-cancel" label={BTN_CANCEL_TODO} variant="contained" onClick={handleCancelTask} />
          <TButton className="btn-update" label={BTN_UPDATE_TODO} variant="contained" onClick={handleUpdateTask} />
          <TButton className="btn-create" label={BTN_COMPLETE_TODO} variant="contained" onClick={handleCompletedTask} />
        </Grid>
      )
    )
  }

  const renderEditTask = (): JSX.Element | false => {
    return (
      displayEditModal && (
        <EditTask
          id={Number(id)}
          displayModal={displayEditModal}
          title={title}
          setDisplayModal={(status, isUpdate) => toggleEditModal(!status, '', isUpdate)}
        />
      )
    )
  }

  if (!id || !item.name) {
    return <></>
  }

  return (
    <>
      <TTitle className="page-title" title={PAGE_DETAIL} variant="h1" />

      <Paper elevation={3} className="content mt-10">
        {renderActionButtons(item.status)}

        <Grid className="title-container" container>
          <TTitle variant="h6" title={item.name} />
        </Grid>

        <div className="page-detail">
          <Grid container>
            <Grid item xs={8}>
              <Grid className="mt-10" container>
                <Grid item xs={3}>
                  <IViewJobType value={item.typeName} />
                </Grid>
                <Grid item xs={3}>
                  <IViewStatus value={item.status} />
                </Grid>
              </Grid>
              <Grid className="mt-30" container>
                <Grid item xs={3}>
                  <IViewPriority value={item.priority} />
                </Grid>
                <Grid item xs={3}>
                  <IViewCompletedTime
                    isAllDay={item.completionAllDay}
                    timeFrom={item.completionTimeFrom}
                    timeTo={item.completionTimeTo}
                    timeDate={item.completionTimeDate}
                  />
                </Grid>
                <Grid item xs={4}>
                  <IViewReminder value={item.reminderTime} />
                </Grid>
              </Grid>
              <Grid className="mt-30" container>
                <IViewDescription value={item.description} />
              </Grid>
              <Grid className="mt-20" container>
                <Grid item xs={6}>
                  <IViewRelatedTo relatedTo={item.relatedTo} relatedType={item.relatedType} />
                </Grid>
                <Grid item xs={6}>
                  <IViewLocation value={item.location} />
                </Grid>
              </Grid>
              <Grid className="mt-30" container>
                <Grid item xs={3}>
                  <IViewAssigner label={LABEL_ASSIGNER} value={item.assignorId} assignorName={item.assignorName} />
                </Grid>
                <Grid item xs={3}>
                  <IViewCreatedDate label={LABEL_ASSIGNED_DATE} value={item.createdDate} />
                </Grid>
                <Grid item xs={3}>
                  <IViewAssignedTo
                    label={LABEL_ASSIGNEE}
                    value={item.assignedTo}
                    assignedToName={item.assignedToName}
                  />
                </Grid>
                <Grid item xs={3}>
                  <IViewCreatedDate label={LABEL_ASSIGNED_UPDATED_DATE} value={item.updatedDate} />
                </Grid>
              </Grid>
              <Grid container>
                <Grid item xs={12}>
                  <TCommentCategoryContent id={Number(id)} status={item.status} />
                </Grid>
              </Grid>
            </Grid>
            <Grid className="mt-40 attached-files" item xs={4}>
              <TLabelValue label="nh km" value={renderAttachedFiles(listFiles, toggleLightBox)} />
            </Grid>
          </Grid>
        </div>
        {renderLightbox(open, listFiles.image, photoIndex, setOpen, setPhotoIndex)}
      </Paper>
      {/* Edit Task */}
      {renderEditTask()}
      {/* Comment task completed */}
      <CommentTask
        displayModal={modalTaskCompleted}
        id={Number(id)}
        createdBy={Number(item.createdBy)}
        featureCode={FeatureCodeType.COMPLETED}
        setDisplayModal={(status) => setModalTaskCompleted(!status)}
        title={TITLE_COMMENT_COMPLETED}
      />
      {/* Comment task canceled */}
      <CommentTask
        displayModal={modalTaskCanceled}
        id={Number(id)}
        createdBy={Number(item.createdBy)}
        featureCode={FeatureCodeType.CANCELED}
        setDisplayModal={(status) => setModalTaskCanceled(!status)}
        title={TITLE_COMMENT_CANCELED}
      />
    </>
  )
}
