/* eslint-disable no-unused-vars */
import React, { useCallback, useContext, useEffect, useState, useMemo } from 'react'

import { CommonContext } from 'context/CommonContext'

import { ISelectAssigneeProps } from 'modules/ISelectAssignee/index.d'

import {
  LABEL_ASSIGNEE,
  RESPONSE_GET_SUCCESS,
  PLACEHOLDER_ASSIGNEE,
  PLACEHOLDER_ASSIGNEE_HAS_VALUE,
} from 'constants/common'

import { parseAssigneddOptions } from 'utils/renderComponent'
import { IOption } from 'utils/form'
import { isNotEmpty } from 'utils/util'

import { getUserListFiltered } from 'services/api/GetOptions'
import { TAutocomplete } from 'components/TAutocomplete'

export const ISelectAssignee: React.FC<ISelectAssigneeProps> = (props) => {
  const { setLoading } = useContext(CommonContext)
  const {
    departmentIds,
    errMsg,
    multiple,
    name,
    placeholder = PLACEHOLDER_ASSIGNEE,
    required,
    teamIds,
    value,
    zoneIds,
    onChange,
    setAssignedToIds
  } = props

  let itemName = 'assignedTo'
  if (name) {
    itemName = name
  }

  const memoPlaceholder = useMemo(() => {
    return isNotEmpty(value) ? PLACEHOLDER_ASSIGNEE_HAS_VALUE : placeholder
  }, [placeholder, value])

  const [optionItems, setOptionItems] = useState<IOption[]>([])

  const fetchData = useCallback(async (): Promise<void> => {
    let parseData = []

    // if ((zoneIds && zoneIds.length > 0) || (departmentIds && departmentIds.length > 0)) {
    setLoading(true)
    const dataPost = {
      departmentIds,
      teamIds,
      zoneIds,
    }
    const resp = await getUserListFiltered(dataPost)
    setLoading(false)

    if (resp.code === RESPONSE_GET_SUCCESS) {
      // check data exist and length
      if (resp.data.length > 0) {
        // parse response items
        parseData = parseAssigneddOptions(itemName, resp.data)
      }
    }
    // }

    // update options
    if (setAssignedToIds) {
      let optsId: number[] = []
      parseData.map((item: IOption) => {
        optsId = optsId.concat(Number(item.value))

        return optsId
      })
      setAssignedToIds(optsId)
    }
    setOptionItems(parseData)
  }, [departmentIds, teamIds, zoneIds])

  useEffect(() => {
    fetchData()
  }, [departmentIds, teamIds, zoneIds])

  const changeAssignorName = (assignorId: number): void => {
    const selectedAssignor = optionItems.find((el) => el.value === assignorId)

    if (selectedAssignor) {
      onChange('assignedToName', selectedAssignor.label)
    } else {
      onChange('assignedToName', '')
    }
  }

  useEffect(() => {
    if (!multiple && typeof value === 'number') {
      changeAssignorName(value)
    }
  }, [optionItems])

  const handleChange = (eName: string, val: null | number | number[]): void => {
    if (multiple) {
      onChange(eName, val as number[])
    } else {
      onChange(eName, val || '')
      changeAssignorName(val as number)
    }
  }

  return (
    <TAutocomplete
      multiple={multiple}
      error={errMsg}
      required={required}
      label={LABEL_ASSIGNEE}
      id={itemName}
      name={itemName}
      option={optionItems}
      placeholder={memoPlaceholder}
      value={value}
      onChange={handleChange}
    />
  )
}
