import React, { ChangeEvent, useContext, useState } from 'react'
import { AttachmentProps } from 'modules/Attachment/index.d'
import { Box, Button, FormHelperText, Grid, IconButton, Typography } from '@material-ui/core'
import HighlightOffIcon from '@material-ui/icons/HighlightOff'

import {
  ACCEPT_FILE_SIZE,
  BTN_UPLOAD,
  ERROR_MAX_FILE_SIZE,
  ERROR_MAX_ATTACHMENT_FILE,
  EXCEL_TYPE,
  FILE_EXTENSION_ACCEPT,
  LABEL_ATTACHMENT,
  ICON_EXCEL,
  ICON_PDF,
  ICON_PPT,
  ICON_WORD,
  MAX_FILE_SIZE,
  PDF_TYPE,
  POWERPOINT_TYPE,
  RESPONSE_GET_SUCCESS,
  WORD_TYPE,
} from 'constants/common'

import 'modules/Attachment/styles.scss'
import { uploadFile } from 'services/api/Task'
import { AttachedFile } from 'utils/form'
import { CommonContext } from 'context/CommonContext'

const Attachment: React.FC<AttachmentProps> = (props) => {
  const { setLoading } = useContext(CommonContext)

  const { fileList, id, name, onSetFile } = props

  // const [totalFile, setTotalFile] = useState<number>(0)
  const [error, setError] = useState<string | null>('')

  let clsUploadBtn = 'upload-label'
  if (fileList?.length === MAX_FILE_SIZE) {
    clsUploadBtn += ' hide'
  }

  const handleFileChange = async (e: ChangeEvent<HTMLInputElement>): Promise<void> => {
    e.preventDefault()

    const { files }: any = e.target

    // check file is existed
    if (files && files.length > 0) {
      let newMergedFiles: any = files
      if (fileList && fileList.length > 0) {
        newMergedFiles = [
          ...files,
          ...fileList
        ]
      }

      // validate max length 
      if (newMergedFiles.length > MAX_FILE_SIZE) {
        setError(ERROR_MAX_ATTACHMENT_FILE)
        return
      }

      for (let f = 0; f < files.length; f += 1) {
        // size validation
        if (files[f].size > ACCEPT_FILE_SIZE) {
          setError(ERROR_MAX_FILE_SIZE)

          return
        }
      }

      // reset error, validated files
      newMergedFiles = []
      setError('')

      // toggle loading
      setLoading(true)

      const dataForm = new FormData()
      for (let f = 0; f < files.length; f += 1) {
        dataForm.append('files', files[f], files[f].name)
      }

      const resp = await uploadFile(dataForm)
      if (resp.code === RESPONSE_GET_SUCCESS) {
        // toggle loading
        setLoading(false)

        if (fileList) {
          newMergedFiles = fileList.concat(resp.data)
        } else {
          newMergedFiles = newMergedFiles.concat(resp.data)
        }

        // setTotalFile(newMergedFiles.length)
        onSetFile(name, newMergedFiles)
      } else {
        // toggle loading
        setLoading(false)

        // validate from BE response
        setError(resp.message)
      }
    }
  }
  const handleDeleteFile = (url: string): void => {
    if (fileList && fileList.length > 0) {
      const newList = fileList.filter((item) => item.url !== url)

      // setTotalFile(newList.length)
      onSetFile(name, newList)
    }
  }

  const convertToMb = (size: number): string => {
    return `${(size / (1024 * 1024)).toFixed(2)} MB`
  }

  const getIconPath = (file: AttachedFile): string => {
    const toLowercaseType = file.type.toLowerCase()
    if (toLowercaseType) {
      if (WORD_TYPE.includes(toLowercaseType)) {
        return ICON_WORD
      }

      if (EXCEL_TYPE.includes(toLowercaseType)) {
        return ICON_EXCEL
      }

      if (POWERPOINT_TYPE.includes(toLowercaseType)) {
        return ICON_PPT
      }

      if (PDF_TYPE.includes(toLowercaseType)) {
        return ICON_PDF
      }
    }

    return file.url
  }

  const renderFileIcon = (file: AttachedFile): JSX.Element => {
    const iconPath = getIconPath(file)
    return <img className="fileIcon" src={iconPath} alt={iconPath} />
  }

  const getListFile = (list: AttachedFile[]): AttachedFile[] => {
    if (!list) {
      return []
    }
    return Object.keys(list).map((i) => {
      const index: number = +i
      return list[index]
    })
  }
  const renderFileList = (): JSX.Element => {
    if (!fileList || fileList.length === 0) {
      return <></>
    }

    const list = getListFile(fileList)
    return (
      <Box className="fileBox">
        <Grid container spacing={2} className="fileContainer">
          {list.map((f) => {
            return (
              <Grid className="fileGrid" item xs={6} key={`${f.size}-${f.url}`} container>
                <Grid item xs={4} className="iconBox">
                  {renderFileIcon(f)}
                </Grid>
                <Grid item xs={8} className="fileItem">
                  <Typography className="fileName" component="p">
                    {f.name}
                  </Typography>
                  <Typography className="fileSize" component="p">
                    {convertToMb(f.size || 0)}
                  </Typography>
                  <IconButton className="icon-close" onClick={() => handleDeleteFile(f.url)}>
                    <HighlightOffIcon />
                  </IconButton>
                </Grid>
              </Grid>
            )
          })}
        </Grid>
      </Box>
    )
  }

  return (
    <div className="attachment-files">
      <Typography variant="subtitle2" component="p">
        {`${LABEL_ATTACHMENT} (${fileList?.length || 0})`}{' '}
      </Typography>
      {error && <FormHelperText error>{error}</FormHelperText>}
      <div className="upload-btn">
        {renderFileList()}
        <input
          accept={FILE_EXTENSION_ACCEPT.toString()}
          id={id}
          multiple
          style={{ display: 'none' }}
          type="file"
          onChange={handleFileChange}
        />

        <label className={clsUploadBtn} htmlFor={id}>
          <Button component="span" variant="outlined" color="primary">
            {BTN_UPLOAD}
          </Button>
        </label>
      </div>
    </div>
  )
}

export default Attachment
