import React from 'react'

import { ISelectRemiderProps } from 'modules/ISelectRemider/index.d'

import { LABEL_REMINDER, REMIDER_OPTION } from 'constants/common'
import { parseOptions } from 'utils/renderComponent'
import { TAutocomplete } from 'components/TAutocomplete'

export const ISelectRemider: React.FC<ISelectRemiderProps> = (props) => {
  const { errMsg, disabled, value, onChange } = props

  const itemName = 'reminderTime'
  const defItems = parseOptions(itemName, [])
  // parse response items
  let parseData = parseOptions(itemName, REMIDER_OPTION)
  parseData = defItems.concat(parseData)

  return (
    <TAutocomplete
      error={errMsg}
      disabled={disabled}
      id={itemName}
      label={LABEL_REMINDER}
      name={itemName}
      option={parseData}
      value={value}
      onChange={onChange}
      placeholder="-- Chn --"
    />
  )
}
