import React, { useCallback, useContext, useEffect, useState } from 'react'

import { ICommentTask } from 'modules/CommentTask/index.d'

import { TODO_TASK_COMMENT_DEFAULT_VALUE } from 'constants/defaultValue'

import { ITaskCommentData } from 'utils/form'

import { CommonContext } from 'context/CommonContext'

import {
  getCommentCategoryConfig,
  getCommentCategorySuggestions,
  submitCanceledTask,
  submitCompletedTask,
} from 'services/api/Task'
import { FeatureCodeType, RESPONSE_GET_SUCCESS } from 'constants/common'
import { CategoryConfig, CommentSuggestion } from 'model/comment'
import { TCommentForm } from 'components/TCommentForm'
import { CompleteTaskConfettiPopup } from 'components/CompleteTaskConfettiPopup'

const CommentTask = (props: ICommentTask): JSX.Element => {
  const { setLoading } = useContext(CommonContext)

  const { displayModal, createdBy, id, setDisplayModal, featureCode, title } = props
  const [commentTask, setCommentTask] = useState<ITaskCommentData>(TODO_TASK_COMMENT_DEFAULT_VALUE)
  const [categoryConfig, setCategoryConfig] = useState<CategoryConfig[]>([])
  const [listCommentSuggestion, setListCommentSuggestion] = useState<CommentSuggestion[]>([])
  const [displayConfettiPopup, setDisplayConfettiPopup] = useState<boolean>(false)

  // callback reset all function
  const resetAll = useCallback((): void => {
    // Reset all select child
    setCommentTask(TODO_TASK_COMMENT_DEFAULT_VALUE)
  }, [])

  useEffect(() => {
    return () => {
      resetAll()
    }
  }, [displayModal, resetAll])

  useEffect(() => {
    const fetchCategoryConfig = async (): Promise<void> => {
      const response = await getCommentCategoryConfig(featureCode)
      if (response.result) setCategoryConfig(response.data)
    }

    const fetchCategorySuggestion = async (): Promise<void> => {
      const response = await getCommentCategorySuggestions(featureCode)
      if (response.result) setListCommentSuggestion(response.data)
    }

    if (featureCode) {
      fetchCategoryConfig()
      fetchCategorySuggestion()
    }
  }, [featureCode])

  const handleCloseModal = (modal: boolean): void => {
    setDisplayModal(modal)
  }

  const handleClosePopup = (): void => {
    setDisplayConfettiPopup(false)
    window.location.reload()
  }

  const handleCommentTask = async (commentData: ITaskCommentData): Promise<void> => {
    // set Loading
    setLoading(true)

    // For complete
    if (featureCode === FeatureCodeType.COMPLETED) {
      const dataPost = {
        completedComment: commentData,
        taskId: id,
      }
      const response = await submitCompletedTask(dataPost)

      setLoading(false)

      if (response && response.code === RESPONSE_GET_SUCCESS) {
        handleCloseModal(displayModal)
        setDisplayConfettiPopup(true)
      }
    }

    // For cancel
    if (featureCode === FeatureCodeType.CANCELED) {
      const dataPost = {
        canceledComment: commentData,
        taskId: id,
      }
      const response = await submitCanceledTask(dataPost)
      setLoading(false)

      if (response && response.code === RESPONSE_GET_SUCCESS) {
        // close modal
        handleCloseModal(displayModal)
        window.location.reload()
      }
    }
  }

  return (
    <>
      <TCommentForm
        displayModal={displayModal}
        id={id}
        item={commentTask}
        createdBy={createdBy}
        categoryConfig={categoryConfig}
        listCommentSuggestion={listCommentSuggestion}
        handleCloseModal={handleCloseModal}
        handleSubmitForm={handleCommentTask}
        setDisplayModal={setDisplayModal}
        title={title}
      />
      <CompleteTaskConfettiPopup show={displayConfettiPopup} handleClose={handleClosePopup} />
    </>
  )
}

export default CommentTask
